.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

.header {
  position: fixed;
  top: 0;
  z-index: 800;
  width: 100%; }
  .header .container {
    position: relative;
    margin-bottom: 0;
    height: 3.4375rem;
    background-color: #242424;
    padding: 0.6875rem 0.6875rem 0 0;
    text-align: right;
    box-sizing: border-box; }
  .header__logo {
    position: absolute;
    top: 0;
    left: 0;
    display: inline-block;
    padding: 0.875rem 0 0 1rem; }
    .header__logo a {
      display: block;
      width: 4.375rem;
      height: 1.6875rem;
      background: url("../img/common/logo.svg") no-repeat 50%;
      background-size: contain; }
  .header__alarm, .header__searchBtn, .header__mdschIcon {
    margin-left: 0.3125rem; }
  .header__alarm {
    position: relative;
    display: inline-block;
    padding: 0.375rem;
    text-align: center;
    line-height: 1; }
    .header__alarm:after {
      content: '';
      display: block;
      display: inline-block;
      width: 17.5px;
      height: 19px;
      background-position: -314px -24.5px;
      background-size: 332px 303px;
      background-image: url("../img/common/sprite.png?v20210622");
      background-repeat: no-repeat; }
    .header__alarm .num {
      position: absolute;
      right: 0;
      top: 0;
      width: auto;
      min-width: 1rem;
      height: 1rem;
      border-radius: 1rem;
      background-color: #f72053;
      color: #fff;
      text-align: center;
      font-size: 0.625rem;
      line-height: 1rem; }
  .header__searchBtn {
    right: 5.75rem;
    display: inline-block;
    padding: 0.375rem;
    line-height: 1; }
    .header__searchBtn:after {
      content: '';
      display: inline-block;
      width: 18px;
      height: 19.5px;
      background-position: -314px 0px;
      background-size: 332px 303px;
      background-image: url("../img/common/sprite.png?v20210622");
      background-repeat: no-repeat; }
  .header__mdschIcon {
    padding: 0.375rem;
    display: inline-block;
    line-height: 1; }
    .header__mdschIcon:after {
      content: '';
      display: inline-block;
