@charset "UTF-8";

.page_detail {
    background: #fff;
    // min-height: calc(100vh - 120px); // ) 50,  70
    // min-height: calc(100vh - 117px); // 51,  66

    .card__btns {
        margin-bottom: -1px; // fix footer  
    }
}

@mixin detail-rt(){
    position: absolute;        
    top:0;
    right:0;
    padding: 0 rem(32);
    height: rem(110);
    box-sizing: border-box;
    color: #333333;
    > * {
        display: inline-block;
        vertical-align: middle;
    }
    &:after {
        content:'';
        display: inline-block;
        vertical-align: middle;
        height:100%;
    }
}
@mixin detail-lt(){
    position: absolute;        
    top:0;
    left:0;
    padding: 0 rem(28);
    height: rem(110);
    box-sizing: border-box;
    color: #333333;
    > * {
        display: inline-block;
        vertical-align: middle;
    }
    &:after {
        content:'';
        display: inline-block;
        vertical-align: middle;
        height:100%;
    }
}
.detail {
    &__profile,
    &__header {
        position: relative;
        padding: rem(30) $commSidePadding 0 $commSidePadding; 
        .badgebox {
            margin-bottom: rem(16);
            line-height:1;
        }
    }
    &__subject {
        margin-bottom: rem(30);
        word-break: break-all;
        + .profilefield {
        }
    }
    &__title {
        font-size: rem(36);
        line-height: $defLineHeight;
        color:#333;
        @include bold();
    }
    &__sideinfo {
        // margin-top: rem(24);
        margin-bottom: rem(16);
        padding-bottom: rem(22);
        border-bottom:1px solid #e6e6e6;
        font-size: rem(28);
        line-height:1;
        color:#333;
    }    
    &__body {
        overflow: hidden;
        margin-top: rem(6);
        padding: 0 $commSidePadding 0;
        font-size: rem(32);
        color:#333;
        word-break: break-all;
        p {
            margin: rem(12) 0 rem(16) 0;
            font-size: 1rem !important;
            line-height: 1.5em;
        }
        img,
        video {
            display: block;
            margin:0 auto;
            max-width: 100%;
        }
        [class^=atchbox__] {
            &:not(:first-child){
                margin-top: rem(14);
            }
        }
        
        .atchbox__gallery ,
        .atchbox__link {
            margin:0 -#{$commSidePadding};
        }
    }    
    &__image,
    &__movie {
        display: block;
        margin:rem(14) -#{$commSidePadding};
    }
    &__movie {
        position: relative;
        z-index: 1;
        
        &:before {
            .detail__body & {
                display: none; //    
            }
            @extend %xeicon;
            position: absolute; 
            left: 50%; top: 50%;       
            font-size: 20vw;
            margin-left: -10vw;
            margin-top: -10vw;
            color: #fff; 
            z-index: 1;
        }
        @extend .xi-play-circle-o;
    }    
    &__topline { //  
        position: sticky;
        position: -webkit-sticky;
        // position: fixed;
        top:0;
        z-index:10;
        overflow: hidden;
        height: rem(110);
        box-sizing: border-box;
        font-size:0;
        text-align: center;
        background-color: #fff;
        transition: box-shadow .8s;
        user-select: none;
        &.line {
            box-shadow: 0 0 rem(6) rgba(0,0,0,.1);
            height: rem(110) !important;

            display: block;width: auto !important;margin: 0 !important;border-radius: unset;text-indent:0 !important;
        }
        &:after {
            content: '';
            display: inline-block;
            height: 100%;
            vertical-align: middle;
        }
        h2 {
            display: inline-block;
            vertical-align: middle;
            font-weight: normal;
            font-size: rem(34) !important;
            max-width:calc(100% - #{rem(200)});
            height: rem(110);
            line-height: rem(114);
            color: #333;
            @include ellipsis();
        }
        .lt {
            position: absolute;
            top: 0;
            left: 0;
        }
        .rt {
            position: absolute;
            top: 0;
            right: 0;
        }
        .btn_close {
            display: inline-block;
            box-sizing: border-box;
            padding: rem(16,1) rem(16,1) rem(13,1) rem(8,1);
            height: rem(110);
            i {
                font-weight: bold;
                font-size: rem(48);
                color: #333;
            }
        }
    }
    &__btnmore {
        position: absolute;
        top: 0;
        right: 0;
        width: 50px; 
        height: 100%; 
        text-align: right; 
        color: #a0a0a0; 
        line-height: 1; 
        &:after {
            content: '';
            display: inline-block;
            height: 100%;
            vertical-align: middle;
        }

        .xi-ellipsis-h {
            position: absolute;
            top:calc(50% - 2px);
            right:rem(36);
            font-size: rem(44);
            color: #333333;
            line-height: 1;
        }
        .icon_more {
            position: absolute;
            top:calc(50% - 2px);
            right:rem(36);
            @include _003_icon_more_detail;
        }
        select { 
            display: inline-block; 
            position: absolute; 
            top: 0; right: 0;
            background:transparent;
            color:transparent !important;
            border:none !important;
            width: 45px; 
            height: 100%; 
            margin: 0; 
            padding: 0; 
            font-size: 12px; 
            line-height: 1; 
        }
        & + .detail__rt {
            right: 50px;
            padding-right: rem(16);
        }
    }
    &__btnback {
        position: absolute;
        top:0;
        left:0;
        height: rem(110);
        box-sizing: border-box;
        padding: 0 5px 0 14px;
        .xi-angle-left {
            display: inline-block;
            vertical-align: middle;
            font-size: rem(42);
            color: #000000;
        }
        &:after {
            content:'';
            display: inline-block;
            vertical-align: middle;
            height:100%;
        }
    }
    &__lt {
        @include detail-lt();
        z-index:10;
        font-size: 24px;
        color: #333333;
        
        .xi-angle-left {
            display: inline-block;
            vertical-align: middle;
            font-size: rem(42);
            color: #000000;
        }
    }
    &__rt {
        @include detail-rt();
        left: auto !important;
        z-index:10;
        font-size: 24px;
        color: #333333;
        &.ManagerBtn {
            padding-top: rem(12);
            font-size: rem(24);
        }
    }
}

.icon_setting {
    @include _003_icon_setting;
}
.icon_share {
    @include _003_icon_share;
}

// subpage   modal
.subPage__modal{
    display: block; 
    overflow-y: scroll; 
    padding-top: 3.4375rem; 
    padding-bottom: 0; 
    position: fixed; top: 0; left: 0; bottom: 0; right: 0; 
    z-index: 10; 
    width: 100%; 
    height: 100vh;
    .detail__topline, .detail__topline.line{
        position: fixed;
        z-index: 129;
        width: 100% !important;
    }
}