@charset "UTF-8";


.nodata {
    padding:rem(150) 4%;
    text-align: center;
    font-size: 15px;
    line-height: 1.4em;
    color: #333;

    .profilefield.type_verticalMsg + & {
        padding-top: rem(50);
    }
}
.btn_line {
    display: inline-block;
    vertical-align: middle;
    width: 80px;
    height:33px;
    text-align: center;
    font-size: 12px;
    line-height: 0;
    padding: 2px 4px 0 4px;
    box-sizing: border-box;
    background-color: #fff;
    border: 1px solid #e4e4e8;
    color: #242424;
    border-radius: 35px;
    cursor: pointer;
    &:hover,&:active {
        background-color: #fafafa;
    }
    .btn_myVdtMr + & {
        margin-left: rem(10);
    }
}


.profile_top {
    overflow: hidden; 
    padding:0 20px 0 0;
    height:rem(72); // 210812 /  /  rem(92);
    border-bottom:1px solid #f3f3f3; // 210812 /  /  #e4e4e8;
    background: #fff;

    .vDtMb__tabBox + & {
        border-top: rem(16) solid $subLineColor;
    }

    // 210812 /  /   
    .rt {
        float: right;
        font-size:rem(24);
        line-height: rem(30);
        margin-top: rem(20);
    }
    
    .profile_topSave {
        float: right;
        font-size:rem(26);
        line-height: 15px;
        margin-top: 15px;
        color: $pointColor;
        font-weight: bold;
        &:before {
            content:'';
            position: relative;
            top:1px;
            margin-right: rem(12);
            display: inline-block;
            @include my-vdetailing-mrhistory-filter-save(28,28);
        }
    }
    .profile_topSet {
        float: right;
        font-size:rem(26);
        line-height: 15px;
        margin-top: 15px;
        color: #242424;
        font-weight: bold;
        &:before {
            content:'';
            position: relative;
            top:1px;
            margin-right: rem(12);
            display: inline-block;
            @include career-filter(24,24);
        }
    }
}
.c_blue {
    color: $pointColor !important;
}

.profile_list {
    padding:0 $commSidePadding;

    .childTopsticky + & {
        border-top: rem(16) solid $subLineColor;
    }
    
    .profilefield__badge {
        vertical-align: middle;
    }
}
.profile_list li {
    padding:rem(34) 0;
    &:not(:last-child) {
        border-bottom: solid 1px rgba(0,0,0,0.05)!important;
    }
}
.profile {
    &__fixBtn {}
    &__grayBox {
        margin-top: rem(24);
        padding: rem(16) rem(22);
        border-radius: rem(12);
        font-size: rem(24);
        color: #6c6c78;
        background: #f8f8fa;

        &--bar {
            display: inline-block;
            margin: 0 rem(10);
            width: 1px;
            height: rem(24);
            vertical-align: middle;
            background: #e4e4e8;
        }
    }
}

.message__typeReply {
    padding: rem(36) $commSidePadding rem(32); 
    border-top: 1px solid #e9e9e9;
    background: #fff;
    .page_detail + & {
        // margin-top: 1px;
    }
}
.message {
    overflow: hidden;
    padding: 0 rem(44) rem(32) rem(32);
    box-sizing: border-box;
    min-height: calc(100vh - #{rem(240)} ); //  rem(110) +  .commentWrite rem(132);
    background: #e7e7eb;

    .profilefield.type_verticalMsg {
        margin: rem(62) 0 rem(54);
    }

    &__date {
        margin: rem(32) 0;
        text-align: center;
        font-size:0;
        line-height: 1;
        span {
            display: inline-block;
            padding:0 rem(20);
            height: rem(56);
            line-height: rem(56);
            border-radius: rem(28);
            font-size: rem(22);
            color:#fff;
            background: #c2c2c9;
        }
    }
    &__list {
        &.ty_reply { //   comment   
            li{
            }
        } // ty_reply
    }
    &__btns {
        position: absolute;
        top:10px;
        right:0;
        .btnIcon {
            margin-left:rem(16);
        }
    }
    &__item {
        // display: flex;
        position: relative;
        overflow: hidden;
        margin-bottom:rem(24);
        font-size: 0;
        line-height: 1;
        .message__typeReply & {
            &:not(:first-child){
                margin-top: rem(26);
            }
            .btn_select {
                position: absolute;
                top:10px;
                right:10px;

                .xi-ellipsis-h {
                    color:#a0a0a0;
                }
            }
            .profilefield.type_small {
                margin-bottom: rem(18);
            }
            .message__cont {
                padding:0 rem(8) 0 rem(78);
                width: 100%;
                box-sizing: border-box;
            }
        }
        .message & {
            overflow: visible; //    visible
            padding:0 0 0 rem(90);

            &.left + .message__item.left,
            &.right + .message__item.right {
                margin-bottom: rem(8);
            }
            .profilefield__img {
                position: absolute;
                top:0;
                left:0;
                width:rem(70);
                height:rem(70);
            }
            .message__cont {
                overflow: visible; //    visible
                max-width:calc(80vw - 64px);
            }
            .message__body {
                border-radius: rem(12);
                padding: rem(20) rem(20) rem(18) rem(20);
                background-color:#fff;
            }
        }
        &.depth2 {
            margin-left: rem(62);
        }
        &.right {
            display: flex;
            flex-direction: row-reverse;
            float:none !important;
            .message__body {
                position: relative;
                float: right;
                color:#fff;
                background-color: #4d90dd;

                &::before {
                    content:'';
                    display: inline-block;
                    position: absolute;
                    top:rem(24);
                    right:- rem(12);
                    width: 0;
                    height: 0;
                    border-bottom: rem(14) solid transparent;
                    border-left: rem(12) solid #4d90dd;
                }
            }
            .message__aside {
                margin-right:rem(8);
                text-align: right;
            }
            .message__link {
                color: #fff;
            } 
        }
        &.left {
            display: flex;
            float:none !important;
            width:calc(100% - #{rem(44+32)}); // 44+32
            box-sizing: content-box;
            .message__aside {
                margin-left:rem(8);
                text-align: left;
            }
            .message__body {
                position: relative;
                &::before {
                    content:'';
                    display: inline-block;
                    position: absolute;
                    left:- rem(12);
                    width: 0;
                    height: 0;
                    border-bottom: rem(14) solid transparent;
                    border-right: rem(12) solid #fff;
                }
            }
        }
    }
    
    .atchbox__img,
    .atchbox__movie,
    .atchbox__file {
        float: left;
    }
    &__cont {
        overflow: hidden;
        display: inline-block;
        position: relative;
    }
    &__body {
        position: relative;
        display: block;
        box-sizing: border-box;
        font-size:rem(28);
        line-height: 1.286em;
        color:#333;
        word-break: break-word;
        
        .message__typeReply & {
            font-size: rem(30);
            line-height: 1.5em;
        }

        + .message__atchbox {
            padding-top: rem(8);
        }
        .tag {
            color:#525252;
            font-weight: bold;
        }
    }
    &__link {
        display: inline-block;
        margin:0;
        text-decoration: underline;
        &:hover {text-decoration: underline;}
    }
    &__aside {
        display: inline-block;
        padding-bottom:rem(8);
        width:60px;
        text-align: right;
        vertical-align: bottom;
        white-space: nowrap;
        line-height:1;
        font-size:11px;
        color:#a0a0a0;
        .asidewrap {
            display: inline-block;
            vertical-align: bottom;
            span {
                display: block;
            }
        }
        .unread {
            display: block;
            margin-bottom: rem(11);
            font-size: rem(22);
            color: #4d90dd;
        }
        .time {}
        &:after {
            content: '';
            display: inline-block;
            height: 100%;
        }
    }
    &__atchbox {
        clear: both;
        overflow: hidden;
        width: calc(80vw - 64px);
        .image ,.file {
            &:not(:last-child){
                margin-bottom: rem(8);
            }
        }
        .image {
            position: relative;
            display: block;
            border-radius: rem(12);
            max-width: 400px;
            width:100%;
            @include ratioWidth(100,498,300);
            @include c_img();
        }
        .xi-play-circle-o {
            position: absolute; 
            left: 50%; top: 50%; 
            font-size: rem(100);
            margin-left: - rem(50);
            margin-top: - rem(50);
            color: #fff; 
            z-index: 1;
        }
        .file {
            position: relative;
            display: block;
            overflow: hidden; 
            clear: both;
            text-align: left;
            box-sizing: border-box;
            padding: 0 rem(20);
            height: rem(70);
            border-radius: rem(12);
            font-size:rem(22);
            color: #333; 
            background-color: #f4f4f4; 
            .filewrap {
            }
            .file_name ,
            .file_extension,
            .file_size {
                display: inline-block;
                line-height: rem(70);
                vertical-align: middle;
            }
            .file_extension {
                &::before {
                    content:'.';
                }
            }
            .file_name {
                position: relative;
                padding-left: rem(30);
                max-width: calc(100% - 94px);
                line-height: rem(70);
                white-space:nowrap;
                text-overflow:ellipsis;
                overflow:hidden;
    
                .xi-attachment {
                    position: absolute;
                    top:1px;left:0;
                    margin:0 2px 0 0;
                    line-height: rem(64);
                    transform: rotate(-46deg);
                    font-size: rem(24);
                    color:#595959;
                }
            }
            .file_size {    
                position: absolute;
                top: 0;
                right: 10px;
                margin-left:rem(20);
                color: #9f9f9f;
            }
        }
    }
    &__atchboxList { //  
        overflow: hidden;
        clear: both;
        margin-top:rem(20);
        margin-left: - rem(14);
        line-height: 15px;

        .image ,.file {
            margin: 0 0 rem(14) rem(14); 
        }
        .image {
            overflow: hidden;
            width: 70px; height: 70px; 
            display: inline-block; 
            position: relative;
            vertical-align: top;
            cursor: pointer;
            @include c_img();
        }
        .xi-play-circle-o {
            position: absolute; 
            left: 50%; top: 50%; 
            font-size: rem(76);
            margin-left: - rem(38);
            margin-top: - rem(38);
            color: #fff; 
            z-index: 1;
        }        
        .file {
            display: block;
            overflow: hidden; 
            clear: both;
            box-sizing: border-box;
            padding:13px 12px 12px;
            height: auto; 
            border-radius: 6px;
            text-align: left;
            font-size:14px;
            color: #333; 
            background-color: #f0f1f3; 
            
            .filewrap {
                display: flex;
            }
            .file_name {
                display: inline-block;
                flex: 1;
                position: relative;
                padding-left: 17px;
                line-height: 1.25em;
                word-wrap: break-word;
                word-break: break-all;
    
                .xi-attachment {
                    position: absolute;
                    top:1px;left:0;
                    margin:0 2px 0 0;
                    transform: rotate(-46deg);
                    font-size: 15px;
                    color:#555;
                }
            }
            .file_size {
                float: right;
                margin-left:25px;
                color: #a0a0a0;
                font-size: 11px;
                line-height: 1.5em;
            }
            .delete {
                // @extend %xeicon;
                font-family: 'xeicon' !important;
                display: inline-block;
                speak: none;
                font-style: normal;
                font-weight: normal;
                font-variant: normal;
                text-transform: none;
                line-height: 1;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
                // @extend .xi-close-min ;
                :before {
                    content: "\e922";
                }

                font-size:18px;
                line-height:15px;
                color:#5d5d5d;
                background: transparent;
                cursor: pointer;
            }
        }
    }
    &__repbtn {
        overflow: hidden;
        margin-top: rem(14);
        .btn_like {
            display: inline-block;
            float: left;
            vertical-align: middle;
            font-size:rem(24);
            line-height: rem(38);
            a {display:inline}
            span {
                color: #5d5d5d;
            }
            &:after {
                content:'';
                display: inline-block;
                margin: 0 0 0 rem(16);
                width:1px;
                height:11px;
                background: #e6e6e6;
            }
            &.on {
                span {
                    color:$pointColor;
                }
            }
        }
        .btn_reply {
            display: inline-block;
            margin: 0 0 0 rem(16);
            float: left;
            vertical-align: middle;
            font-size:rem(24);
            line-height: rem(38);
            span {
                color: #5d5d5d;
            }
        }
        .date {
            display: inline-block;
            margin: 0 0 0 rem(16);
            float: left;
            vertical-align: middle;
            font-size:rem(24);
            line-height: rem(38);
            span {
                color: #a0a0a0;
            }
        }
        .likedata {
            display: inline-block;
            margin: 0 0 0 rem(16);
            float: left;
            vertical-align: middle;
            min-width:rem(64);
            height:rem(32);
            border:1px solid #d6d6d6;
            padding:rem(4) rem(12);
            font-size: rem(20);
            color:$pointColor;
            line-height: 1;
            background: #fff;
            border-radius: rem(64);
            box-sizing: border-box;
            i {
                font-size: rem(22)!important;
            }
            .no {
                font-size: rem(22)!important;
                color:$pointColor;
            }

            &.on {}
        }
    }
    &__iframe {
        height: calc(100vh - #{rem(110)}); //  rem(110) +  .commentWrite rem(132);
        overflow: hidden;
        iframe {
            width: 100%;
            height: 100%;
        }
    }
} // .message

// v-Detailing card
.vdCard {
    &__item {
        overflow: hidden;
        width:100%;
        min-width:calc(80vw - 64px);
        border-radius: rem(12);

        + .message__body {
            margin-top: rem(12);
        }
    }
    &__img {
        position: relative;
        display: block;
        width:100%;
        height:0;
        padding-bottom: 43.75%;
        @include c_img();
    }
    &__textbox {
        display: block;
        padding: rem(20) rem(24) rem(16);
        background: #fff;
    }
    &__date {
        font-size: rem(28);
        line-height: rem(42);
        color: #242424;
        font-weight: bold;
    }
    &__min {
        margin-left: 16px;
        color: #6c6c78;
        font-size: rem(28);
    }
    &__desc {
        display: block;
        margin-top: rem(6);
        height:rem(88);
        font-size: rem(28);
        line-height: 1.286em;
        color: #2e2e2e;
        @include ellipsisV(2);
    }
}

.reply {
}
.commentWrite {
    position: fixed;
    bottom: 0; left: 0; right: 0;
    box-sizing: border-box;
    margin: 0 auto;
    padding:rem(24) rem(128) rem(24) rem(32);
    width: 100%;
    min-width: 320px;
    height: auto;
    min-height:rem(132);
    background-color: #fafafa;
    z-index: 700;
    border-top: 1px solid rgba(0,0,0,0.1);
    >form {
        position: static;
    }
    &__content {
        position: relative;
        padding: 0 rem(104) 0 rem(34);
        border: 1px solid #e1e1e1;
        border-radius: 20px;
        font-size:12px;
        line-height: 17px;
        color:#333;
        text-align: left;
        background: #fff;
    }  
    &__textarea {
        overflow: scroll;
        box-sizing: border-box;
        border: none;
        padding:4px 0 0 0 !important;
        width: 100%;
        height: 25px;
        max-height: 100px;
        font-size:rem(28);
        color:#333333;
        text-align: left;
        background: transparent;
        z-index: 10;
        border-radius: 0px;
        outline: none;
        * {display: inline;}
        &:focus {
            .placeholder {
                color:#afafaf;
            }    
        }
        &::placeholder {
            font-size:rem(28) !important;
            color:#afafaf !important;
        }
    }
    &__input {
        position: relative;
        box-sizing: content-box;
        line-height: 1;
        padding:rem(12) 0 rem(12) 0;
        min-height:27px;
    }
    &__atchbox {
        overflow: hidden;
        margin-top: rem(26);
        width:100%;
        & + & {
            margin-top: 0;
        }
        .image ,.file {
            margin-bottom: 1.3%;
        }
        .file:last-child {
            margin-bottom:0;
        }
        .image {
            position: relative;
            display: block;
            width:24.025%;
            margin-right: 1.3%;
            float: left;
            border-radius: rem(4);
            @include ratioWidth(24.025,106,106);
            @include c_img();
            &:nth-of-type(4n) {
                margin-right:0;
            }
            .delete {
                position: absolute;
                box-sizing: border-box;
                width: 100%;
                height: 100%;
                top: 0;
                right: 0;
                z-index: 1;
                padding: 4px;
                text-align: right;
                font-size: rem(20);
                color:#fff;
                background: rgba(0,0,0,0.6);
            }
        }
        .xi-play-circle-o {
            position: absolute; 
            left: 50%; top: 50%; 
            font-size: 21px;
            margin-left: -10px;
            margin-top: -10px;
            color: #fff; 
            z-index: 1;
        }
        .file {
            position: relative;
            overflow: hidden; 
            clear: both;
            display: block;
            width: 100%;
            text-align: left;
            box-sizing: border-box;
            padding: 0 rem(50) 0 rem(20);
            height: rem(62);
            border-radius: rem(8);
            font-size:rem(22);
            color: #333; 
            background-color: #f4f4f4; 
            .filewrap {
            }
            .file_name ,
            .file_extension {
                display: inline-block;
                line-height: rem(62);
                vertical-align: middle;
            }
            .file_extension {
                &::before {
                    content:'.';
                }
            }
            .file_name {
                display: inline-block;
                position: relative;
                max-width: calc(100% - 43px);
                padding-left: rem(30);
                white-space:nowrap;
                text-overflow:ellipsis;
                overflow:hidden;
    
                .xi-attachment {
                    position: absolute;
                    top:0;left:0;
                    margin:0 2px 0 0;
                    line-height: rem(64);
                    transform: rotate(-46deg);
                    font-size: rem(24);
                    color:#595959;
                }
            }
            .delete {
                position: absolute;
                top: 50%;
                right: 10px;
                z-index: 1;
                margin: -6px 0 0 0;
                text-align: right;
                font-size: rem(20);
                color:#333333;
            }
        }
    }
    &__btn {
        position: absolute;
        bottom:12px;
        right:rem(32);
        font-size: 0;
        line-height: 1;
        .btn_msg {
            display: inline-block;
            margin-left: rem(14);
            width: rem(80);
            height: rem(80);
            line-height: rem(80);
            color: #fff;
            font-size: 0;
            border-radius: 40px;
            border: none;
            background-color: #a0a0a0;
            background-image: url($url + 'common/icon_send.png');
            background-size: 22px auto;
            background-position: 50%;
            background-repeat: no-repeat;
            cursor: pointer;
            
            &.on {
                background-color: $pointColor;
            }
        }
    }
    &__atchBtn {
        position: absolute;
        bottom: 3px;
        right:10px;
        display: inline-block;
        width: 30px;
        height: 30px;
        line-height: 29px;
        font-size: 23px;
        cursor: pointer;
        text-align: center;
        label { // button > label 
            pointer-events: none;
        }
        .xi-paperclip {
            font-size:22px;
            color:#afafaf;
            transform: rotate(46deg);
        }
    }
}

.taglist{
    margin: 0 4px 0 2px;
    width: auto;
    height: 25px;
    line-height: 25px;
    background: #fffce1;
    padding: 3px 10px;
    border-radius: 25px;
    cursor: initial;
    vertical-align: middle;
    border: 1px solid #e1e1c9;
    font-size: 12px;
    white-space: nowrap;
    & + & {margin-left:-5px;}
    .delete {
        display: inline-block;
        margin-left: 5px;
        font-size: 13px;
        cursor: pointer;
    }
}

%autotagStyle {    
    $borderColor : #92c3d2;
    $bgColor : #dbeef4;
    $overColor : #81c3d8;

    min-width:150px;
    border-top: 1px solid $borderColor;
    .autotag__list {
        font-size: 13px;
        line-height: 13px;
        border: 1px solid $borderColor;
        border-top:none;
        background: $bgColor;
        text-align: left;
        box-sizing:border-box;
        white-space: nowrap;

        // &.on {
        //     position: relative;
        //     background: $overColor;
        // }
        &:hover, &.on {
            padding:2px 8px 0;
            border:3px solid $borderColor;
            border-top-width:2px;
        }
    }
}
.autotag {
    // display: none;
    position: absolute;
    top:-75px;
    left:0;
    bottom:auto;
    margin-top: -12px;
    width:240px;
    z-index:100;
    @extend %autotagStyle;
    
    .autotag__list {
        display: block;
        overflow: hidden;
        margin: 0;
        padding:4px 10px 0;
        width:100%;
        height: 25px;
        .text {}
        .rt {
            float: right;
            margin-left:5px;
        }
    }    
} //autotag
.autotag__human {
    position: absolute;
    width:240px;
    z-index:100;
    @extend %autotagStyle;
    .autotag__list {        
        display: block;
        overflow: hidden;
        margin: 0;
        padding:4px 10px 0;
        width:100%;
        height: 40px;
    }
}


@media screen and (min-width:415px){
    .commentWrite__atchbox .image {
        width:18.96%; // (100 - (1.3 * 4)) / 5 +'%';
        @include ratioWidth(18.7,106,106);
        
        &:nth-of-type(4n) {margin-right:1.3%;}
        &:nth-of-type(5n) {margin-right:0;}
    }
}
@media screen and (min-width:500px){
    .message .message__item .message__cont ,
    .message__atchbox .file {
        max-width:336px;
    }
    .message__atchbox .image {
        width:336px;
        padding-bottom: 202px;
    }
    .commentWrite__atchbox .image:not(:last-child), .commentWrite__atchbox .file:not(:last-child) {
        margin-bottom: 4px;
    }
    .commentWrite__atchbox .image {
        width:53px;
        height:53px;
        padding:0;
        &:not(:last-child) {
            margin-right:4px;
        }
    }    
}