@charset 'UTF-8';

.quickWrite {
    position: fixed;
    bottom: rem(144);
    right: rem(24);
    z-index: 10;
    width: rem(110);
    height: rem(110);
    border-radius: 50%;
    background: $pointColor;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,.2);
    &:hover {
        // animation: quickmenuMotion .5s ease; 
    }
    &:after {
        content:'';
    }
    &.-qna {
        &:after {
            @include _002_quick_qna;
        }
    }
    &.-posting {
        &:after {
            @include _002_quick_posting;
        }
    }
    &.-career {
        &:after {
            @include _002_quick_career;
        }
    }
    &.-job {
        &:after {
            @include _002_quick_job;
        }
    }
    &.topMove {
        transition: all 500ms;
        &.active {
            bottom: rem(270);
        }
    }
}

