@charset 'UTF-8';

// rem calc
@function rem($size, $ratio:2) {
    @return ($size/$ratio) / $rootFontsize + rem;
}

// line-height calc
@function lh($fontSize, $lineHeight) {
  @return ($lineHeight/$fontSize) + em;
}

//   % (width:height = 100:x)
@mixin ratioWidth ($percentW, $width, $height){
  height:0;
  padding-bottom: percentage((($height*$percentW)/$width)/100);
}

// vertical ellipsis
@mixin ellipsisV($line){
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: $line;
    -webkit-box-orient: vertical;
    word-wrap: break-word;
}
@mixin ellipsis(){
  white-space:nowrap;
  text-overflow:ellipsis;
  overflow:hidden;
}

// c_img 
@mixin c_img(){
    background-repeat:no-repeat;
    background-size:cover;
    background-position:50%;
}

// font bold
@mixin bold(){
    // font-family: 'AppleSDGothicNeo-SemiBold';
    font-weight: bold;
}


@mixin notTextInput(){
    opacity: 0 !important;
    height: 0 !important;
    width: 0 !important;
    position: absolute !important;
}

@mixin dataURI($imguri) {
  background:url('data:image/png;base64,'+$imguri) no-repeat 0 0;
  background-size: contain;  
}


// mediaquery
@mixin respond-to($media) {
  @if $media == mobile {
    @media only screen and (max-width: $tabletSize - 1) { @content; }
  }
  @else if $media == tablet {
    @media only screen and (min-width: $tabletSize) and (max-width: $pcSize - 1) { @content; }
  }
  @else if $media == pc {
    @media only screen and (min-width: $pcSize) { @content; }
  }
}

// clearboth

@mixin clearboth {
  &::after{
    content: '';
    display: table;
    clear: both;
  }
}