
.sideInfo {
    &Hdr{
        position: relative;
        overflow: hidden;
        padding: rem(60+26+24) 0 rem(40);
        background: #2a2a2a;
        &__btn {
            position: absolute;
            top: rem(26);
            right:rem(24);
            margin-bottom: rem(24);
            text-align: right;
            [class^=sideInfoHdr__btnItem] {
                float: left;
                margin-left: rem(28);
            }
            &Item {
                &--setting ,
                &--close {
                    display: inline-block;
                    padding: rem(10);
                    width: rem(40);
                    height: rem(40);
                    vertical-align: middle;
                    box-sizing: content-box;
                    &:after {
                        content:'';
                        display: inline-block;
                    }
                }
                &--setting {
                    &:after {
                        @include moreSetting(40,40);                    
                    }
                }
                &--close {
                    &:after {
                        margin: rem(2);
                        @include moreOut(38,38);
                    }
                }
            }
        }
    }
    &Prf {
        position: relative;
        overflow: visible; //   box-shadow  
        margin:0 rem(42);
        .profilefield {
            width: calc(100% - #{rem(112)});
        }
        .sideInfoPrf__btnMy {
            position: absolute;
            top:calc(50% - #{rem(66/2)});
            right:0;
            display: block;
            min-width: rem(112);
            height: rem(66);
            line-height: rem(64);
            padding: 0 rem(20);
            box-sizing: border-box;
            text-align: center;
            border-radius: rem(32);
            font-size: rem(26);
            color: #fff;
            border: solid rem(2) #828284;
        }
    }
    &Cont {
        &__title {
            position: relative;
            padding-left: rem(34+16);
            margin: rem(60) $commSidePadding rem(26);
            font-size: rem(38);
            line-height: rem(34); // left icon height
            color: #242424;
            &::after {
                content:'';
                position: absolute;
                top:calc(50% - #{rem(32/2)});
                right:0;
                display: inline-block;
                @include moreArrow(32,32);
            }
            &::before {
                content:'';
                display: inline-block;
                position: absolute;
                top:calc(50% - #{rem(38/2)});
                left:0;
                margin-right: rem(16);
            }
            &.point {
                &::before {
                    @include morePoint(36,36);
                }
            }
            &.event {
                &::before {
                    @include moreEvent(36,36);
                }
            }
        }
    }
    &Point {
        margin: 0 $commSidePadding;
        min-height: rem(276);
        padding: rem(42) rem(24) rem(24);
        box-sizing: border-box;
        border-radius: rem(20);
        box-shadow: 0px 1px 14px 2px rgba(0, 0, 0, 0.06);
        border: solid rem(2) #3f92f0;
        background-color: #ffffff;

        &__top {
            overflow: hidden;
            position: relative;
            margin: 0 rem(22) rem(32);
        }
        &__info {
            p {
                margin-bottom: rem(18);
                font-size: rem(26);
                line-height: 1;
                color: #242424;
            }
            b {
                font-size: rem(44);
                line-height: 1;
                color: $pointColor;
            }
        }
        &__btn {
            position: absolute;
            top:calc(50% - #{rem(70/2)});
            right: 0;
            padding: 0 rem(10);
            min-width: rem(204);
            height: rem(70);
            line-height: rem(72);
            box-sizing: border-box;
            border-radius: rem(35);
            font-size: rem(26);
            color: #fff;
            text-align: center;
            background: #2a2a2a;

            &::before {
                content:'';
                display: inline-block;
                margin-right: rem(12);
                @include moreMall(26,24);
            }
        }
        &__banner {
            img {
                width: 100%;
            }
        }
    }
    &Event {
        margin-left: $commSidePadding;
        &__banner {
            .swiper-slide {
                overflow: hidden;
                width:78.8%; // (750 - $commSidePadding(margin-left 16px)) : 566 = 100 : x
                border-radius: rem(18);
                &:first-child:last-child {
                    width: calc(100% - #{$commSidePadding});
                    box-sizing: border-box;
                }
                &:last-child {
                    margin-right: $commSidePadding !important;
                }
                img {
                    width:100%;
                }
            }
        }
    }
    &Menu {
        display: flex;
        flex-wrap: wrap;
        padding: rem(64) $commSidePadding rem(50);

        [class^=sideInfoMenu__item] {
            flex:1;
            text-align: center;
            &::before {
                content:'';
                display: inline-block;
            }
        }
        
        &__item {
            &--notice::before {
                @include more-notice(52,48);
            }
            &--enquiry::before {
                @include more-enquiry(52,48);
            }
            &--faq::before {
                @include more-faq(54,48);
            }
            &--offer::before {
                @include more-offer(54,48);
            }
        }
        &__text {
            display: block;
            font-size: rem(32);
            color: #242424;
        }
    }
}