@charset "UTF-8";

.myDashboard {
    overflow: hidden;
    position: relative;
    padding: rem(40) rem(32) 0;
    background: #fff;
    .profilefield {
        margin-bottom: rem(60);
    }
    &__inputform {
        position: relative;
        z-index: 1;
        display: inline-block;
        padding-right: rem(8);
        width: rem(120);
        height: rem(120);
        &:after { //  css  content:'.'      
            display: none;
        }
        &.camera:after {
            content: '';
            display: block;
            position: absolute;
            bottom: 0;
            right: 0;
            width: rem(66);
            height: rem(66);
            border-radius: 50%;
            box-shadow: 0 4px 8px 0 rgba(108, 108, 120, 0.1);
            @include _004_icon_myCamera;
        }
    }
    &__inputfile {
        display: block;
        width: rem(120);
        height: rem(120);
        position: absolute;
        background: transparent;
        opacity: 0;
        cursor: pointer;
        top: 0;
        left: 0;
        z-index: 100;
    }
    &__btnMessage {
        position: absolute;
        top:rem(62);
        right:rem(32);
        border-radius: 50%;
        border:1px solid #e4e4e8;
        box-shadow: 2px 2px 12px 0 rgba(108, 108, 120, 0.1);
        @include _004_icon_myMessage;
        background-color: #fff;

        .mrProfile & {
            top:rem(54,1);
        }
        &.v20 {
            @include _004_icon_myMessage_old;
        }
    }
    &__pointBox {
        overflow: hidden;
        box-sizing: border-box;
        margin-bottom: rem(60);
        border-radius: rem(20);
        background: #3f92f0;
        line-height: 1;
    }
    &__pointTitle {
        font-size: 0;
    }
    &__point {
        position: relative;
        display: block;
        margin: 0 rem(32);
        padding: rem(60) 0 rem(60) rem(64);
        font-size: rem(52);
        color: #fff;
        font-weight: bold;
        .icon-myPointArrow {
            display: inline-block;
            position: absolute;
            top:calc(50% - #{rem(22)}); right:0;
            font-weight: bold;
            vertical-align: baseline;
            @include icon-myPointArrow(44,44)
        }
        &:before {
            content:'';
            display: inline-block;
            position: absolute;
            top:calc(50% - #{rem(22)}); left:0;
            @include _004_icon_point();
        }

        &Bn {
            display: block;
            position: relative;
            padding: rem(26) rem(32);
            box-sizing: border-box;
            font-size: rem(26);
            color: #fff;
            background: #76b0f2;
            &:after {
                content:'';
                display: inline-block;
                position: absolute;
                top:calc(50% - #{rem(8)}); right:rem(32);
                @include my-point-go-to-event();
            }
        }
    }
    &__pointBox {
        &.v20 {
            overflow: hidden;
            box-sizing: border-box;
            margin-bottom: 1.875rem;
            padding: 1.0625rem 1.125rem;
            height: 3.0625rem;
            border-radius: 0.25rem;
            background: #f1f6fb;
            line-height: 1;
            
            .myDashboard__pointTitle {
                font-size: 0.9375rem;
                color: #242424;
                position: relative;
                padding-left: rem(42);
                &::before {
                    content: '';
                    position: absolute;
                    left: 0;
                    top: 50%;
                    transform: translateY(-50%);
                    @include _003_icon__point_old();
                }
            }
            .myDashboard__point {
                float: right;
                font-size: 0.9375rem;
                color: #3f92f0;
                font-weight: bold;
                position: static;
                display: inherit;
                margin: 0;
                padding: 0;
                &::before {
                    display: none;
                }
                i {
                    font-weight: bold;
                    vertical-align: baseline;
                }
            }
        }
    }
}

.myMenu {
    + .sideInfoMenu {
        position: relative;
        &:before {
            content:'';
            display: block;
            position: absolute;
            top:0;
            left:0;
            width: 100%;
            height: rem(15);
            border-top: rem(2) solid #e8e8e8;
            background: #f4f4f6;
        }
    }
    &__title {
        margin: 0 0 rem(46) rem(66);
        padding-top: rem(56);
        &:not(:first-child) {
            border-top: rem(2) solid #e2e2e2;
        }
        i {
            vertical-align: middle;
            margin-right: rem(16);
        }
        &Txt {
            position: relative;
            z-index: 1;
            display: inline-block;
            font-size: rem(32);
            font-weight: bold;
            line-height: 1;
            vertical-align: middle;
            color: #242424;
            &:after {
                content:'';
                display: block;
                position: absolute;
                bottom:-2px;
                left:0;
                z-index: -1;
                width: 100%;
                height: 8px;
                background: #3f92f0;
                opacity: .1;
            }
        }
        @at-root {
            .iconMyMu__vd {@include iconMyMu__vd}
            .iconMyMu_qna {@include iconMyMu_qna}
            .iconMyMu_posting {@include iconMyMu_posting}
            .iconMyMu_career {@include iconMyMu_career}
            .iconMyMu_activity {@include iconMyMu_activity}
            .iconMyMu_profile {@include iconMyMu_profile}
            .iconMyMu_quiz {@include iconMyMu_quiz}
            // 220527 :  
            .iconMyMu__coBuying {
                display: inline-block;
                @include iconMyMu_coBuying_icon(36,36)
            }
            // 221018 :   
            .iconMyMu__lecture {
                display: inline-block;
                @include iconMyMu_lecture(36,36);
            }
            // 2306 : vDt   
            .iconMyMu__vDtActivity {
                display: inline-block;
                @include iconMyMu_vDtActivity_icon(36,36);
            }
        }
    }
    &__itemBox {
        overflow: hidden;
        margin:rem(46) 0 rem(28) rem(66);
        li {
            float: left;
            margin-bottom: rem(28);
            width: 50%;
        }
        &Lecture {
            li {
                .myMenu__item {
                    letter-spacing: rem(-1);
                }
            }
        }
    }
    &__item {
        padding: rem(6) rem(6) rem(6) 0;
        font-size: rem(32);
        line-height: 1;
        color: #242424;
    }
}

