.myNavi {
    overflow: hidden;
    padding: 0;
    margin: 0;
    line-height: 45px;
    text-align: center;
    vertical-align: middle;
    box-sizing: border-box;
    background-color: #f7f7f7;

    &__wrap {
        position: relative;
        overflow:hidden;
        margin:rem(18) rem(32);
        border:1px solid #eaeaea;
        border-left: none;
        border-bottom: none;
        background: #fff;
        &.col2 {
            &:after {
                display: none;
            }
            .myNavi__item {
                width: 50%;
            }
        }
        &:after {
            content: '';
            display: block;
            clear: both;
            width: calc(33.33% + 1px);
            border-left: solid #eaeaea 1px;
            border-right: solid #eaeaea 1px;
            display: inline-block;
            height:rem(78);
            box-sizing: border-box;
            position: absolute;
            left: 33.33%;
            bottom: 0;
            pointer-events: none;
        }
        &:before {
            content:''; 
            display:block; 
            width:100%;
            height:0; 
            border-bottom:solid #eaeaea 1px;
            position:absolute; left:0; bottom:0; 
            pointer-events:none; 
        }
    }
    &__item {
        display: block;
        float:left;
        width:33.33%;
        height:rem(78);
        box-sizing: border-box;
        line-height:rem(80);
        box-sizing: border-box;
        border-left:1px solid #eaeaea;
        border-bottom:1px solid #eaeaea;
        font-size:rem(24);
        color: #9f9f9f;
        background:#fff;
        &.on {
            color:#242424;
            font-weight: bold;
        }
    }
}
