.renew {
    &MyDashboard {
        position: relative;
        margin-bottom: rem(100);
    }
    &Profilefield {
        position: relative;
        padding: rem(60) rem(71) rem(184);
        background: #3f92f0;
        box-sizing: border-box;
        &__text {
            max-width: calc(100% - #{rem(216)});
            font-family: 'Noto Sans KR', sans-serif;
            color: #fff;
            > span {
                font-size: rem(38);
                line-height: rem(54);
            }
        }
        &__name {
            font-size: rem(38);
            strong {
                font-size: rem(64);
                font-weight: bold;
                line-height: rem(92);
            }
        }
        &__desc {
            margin: rem(10) 0;
            font-size: rem(30);
            line-height: rem(42);
            color: #5afff9;
        }
        &__point {
            display: flex;
            align-items: center;
            font-size: rem(40);
            line-height: rem(48);
            letter-spacing: rem(-2);
            color: #fff;
            white-space: nowrap;
            strong {
                font-size: rem(54);
                line-height: rem(66);
                letter-spacing: rem(-2.7);
            }
            i {
                margin-left: rem(12);
                font-size: rem(30);
            }
            &Tit {
                position: relative;
                padding-right: rem(36);
                margin-right: rem(30);
                white-space: nowrap;
                &::after {
                    display: block;
                    position: absolute;
                    right: 0; 
                    top: 50%;
                    margin-top: rem(-19);
                    content: '';
                    @include renew_mypage_point_icon(34,34);
                }
            }
            &Info {
                display: flex;
                flex-wrap: wrap;
                align-items: baseline;
                font-family: Verdana;
            }
        }
    }
    &Inputform {
        position: absolute;
        top: rem(60);
        right: rem(72);
        &.camera {
            &::after {
                position: absolute;
                right: 0;
                bottom: 0;
                display: block;
                width: rem(66);
                height: rem(66);
                content: "";
                background: #fff url("data:image/png;base64,") no-repeat center center;
                background-size: rem(32) rem(32);
                border-radius: 100%;
                box-shadow: 0 rem(6) rem(12) 0 rgba(0, 0, 0, 0.16);
            }
        }
        &__file {
            display: block;
            width: rem(216);
            height: rem(216);
            position: absolute;
            background: transparent;
            opacity: 0;
            cursor: pointer;
            top: 0;
            left: 0;
            z-index: 8;
        }
        &__img {
            display: block;
            width: rem(216);
            height: rem(216);
            background-position: center center;
            background-size: cover;
            background-repeat: no-repeat;
            border-radius: 100%;
        }
        &__postingIco {
            position: absolute;
            left: rem(-35);
            bottom: rem(-4);
            width: rem(95);
            height: rem(95);
            z-index: 9;
            a {
                display: block;
                width: 100%;
                height: 100%;
                border-radius: 100%;
            }
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: center;
                border-radius: 100%;
            }
        }
    }
    &MyMenuBox {
        position: absolute;
        left: rem(24);
        bottom: rem(-122);
        padding: rem(40) rem(20) rem(29);
        width: calc(100% - #{rem(46)});
        background: #fff;
        box-sizing: border-box;
        border-radius: rem(20);
        box-shadow: 0 rem(6) rem(12) 0 rgba(0, 0, 0, 0.16);
        overflow: hidden;
        z-index: 9;
        &__swiper {
            margin: 0 rem(20);
            overflow: visible;
            .swiper-slide {
                display: flex;
                justify-content: center;
                align-items: center;
                text-align: center;
                height: rem(104);
                a {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    text-align: center;
                    flex-direction: column;
                    width: 100%;
                    min-height: 100%;
                }
            }
        }
        &__menu {
            &Ico {
                display: block;
                &.pointMallIco {
                    @include renew_mypage_icon01(72,62);
                }
                &.alarmIco {
                    @include renew_mypage_icon02(52,58);
                }
                &.messageIco {
                    @include renew_mypage_icon03(60,54);
                }
                &.settingIco {
                    @include renew_mypage_icon04(54,54);
                }
                &.reilIco {
                    margin-top: rem(-12);
                    @include renew_mypage_icon05(56,64);
                }
                &.lectureIco {
                    @include renew_mypage_icon06(68,54);
                }
                &.scheduleIco {
                    @include renew_mypage_icon07(60,54);
                }
                &.qnaIco {
                    @include renew_mypage_icon08(60,60);
                }
                &.attendanceIco {
                    @include renew_mypage_icon09(58,59);
                }
                .menuCount {
                    position: absolute;
                    left: 53%;
                    top: rem(-20);
                    padding:0 rem(5);
                    width: rem(40);
                    height: rem(40);
                    font-family: 'Noto Sans KR', sans-serif;
                    font-size: rem(20);
                    font-weight: 500;
                    line-height: rem(40);
                    color: #fff;
                    text-align: center;
                    background: #fe0000;
                    border-radius: 100%;
                    box-sizing: border-box;
                }
            }
            &Text {
                display: block;
                font-size: rem(26);
                font-family: Verdana;
                line-height: rem(32);
                color: #020202;
            }
        }
        &__next, &__prev {
            top: rem(20);
            width: rem(40);
            height: rem(144);
            margin-top: 0;
            background-size: rem(20) rem(34);
            background-color: #fff;
            &.removeBtn {
                display: none !important;
            }
        }
        &__next {
