            background-image: url("data:image/png;base64,");
            background-position: 0 center;
            &.swiper-button-disabled {
                background-image:url("data:image/png;base64,");
                opacity: 1;
            }
        }
        &__prev {
            left: 0;
            background-image: url("data:image/png;base64,");
            background-position: right center;
            &.swiper-button-disabled {
                background-image:url("data:image/png;base64,");
                opacity: 1;
            }
        }
        &__bottom {
            padding-top: rem(26);
            margin: rem(27) rem(15) 0;
            border-top: rem(2) solid #d5d5d5;
            a {
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding-left: rem(7);
                font-family: 'Noto Sans KR', sans-serif;
                font-size: rem(26);
                line-height: rem(38);
                color: #656565;
                i {
                    margin-right: rem(8);
                    width: rem(12);
                    height: rem(22);
                    color: #000;
                }
            }
        }
    }
}
