.myWebinar {    
    &__mySchedule {
        font-family: 'Noto Sans KR', sans-serif;
        &Top {
            padding: rem(28) rem(32) rem(32);
        }
        &Date {
            position: relative;
            width: rem(376);
            margin: 0 auto;
            text-align: center;
            &Box {
                position: relative;
                display: inline-block;
                .date {
                    padding: 0 rem(31);
                    width: rem(116);
                    font-size: rem(30);
                    font-weight: bold;
                    color: #000;
                    line-height: rem(44);
                }
                &::after {
                    position: absolute;
                    right: rem(-5);
                    top: 0;
                    content: '\e936';
                    color: #000;
                    font-family: 'xeicon' !important;
                }
            }
            .prevMonth {
                position: absolute;
                left: 0;
                top: 0;
                display: inline-block;
                @include myWebinar_prevMonth_btn(44,44);
            }
            .nextMonth {
                position: absolute;
                right: 0;
                top: 0;
                display: inline-block;
                @include myWebinar_nextMonth_btn(44,44);
            }
            &Pick {
                //left: 50% !important;
                padding: rem(59) rem(45) !important;
                width: calc(100% - #{rem(64)}) !important;
                max-height: unset !important;
                //transform: translateX(-50%);
                box-sizing: border-box;
                background: #fff !important;
                &::before, &::after {
                    display: none !important;
                }
                &.open + &Bg {
                    display: block;
                } 
                &Bg {
                    position: fixed;
                    left: 0;
                    top: 0;
                    display: none;
                    width: 100%;
                    height: 100%;
                    background: rgba(0, 0, 0, 0.4);
                    z-index: 11;
                }
                .flatpickr-months {
                    position: relative;
                    margin: 0 auto rem(43);
