.myPostingProfile {
    &__popup {
        position: fixed;
        left: 0;
        top: 0;
        display: none;
        width: 100%;
        height: 100%;
        z-index: 11;
        &.active {
            display: block;
        }
        &Con {
            position: absolute;
            left: 50%;
            top: 50%;
            padding: rem(29) rem(40) rem(70);
            width: calc(100% - #{rem(32)});
            max-width: rem(560);
            font-family: 'Noto Sans KR', sans-serif;
            background: #fff;
            border-radius: rem(20);
            box-sizing: border-box;
            box-shadow: 0 rem(3) rem(28) 0 rgba(0, 0, 0, 0.16);
            transform: translate(-50%,-50%);
            z-index: 9;
            .tit {
                position: relative;
                margin-bottom: rem(60);
                text-align: center;
                font-size: rem(30);
                color: #000;
                line-height: rem(45);
            }
        }
        &Bg {
            position: fixed;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.6);
            z-index: 1;
        }
        &Close {
            position: absolute;
            right: 0;
            top: 0;
            font-size: rem(34);
        }
    }
    &__box {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        .profile {
            display: block;
            width: 50%;
            padding: 0 rem(10);
            text-align: center;
            box-sizing: border-box;
            .thumb {
                display: block;
                margin: 0 auto rem(16);
                width: rem(140);
                height: rem(140);
                border-radius: 100%;
                overflow: hidden;
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
            .name {
                display: block;
                font-size: rem(36);
                font-weight: bold;
                color: #000;
                line-height: rem(54);
                @include ellipsis;
            }
            .desc {
                display: block;
                font-size: rem(24);
                color: #555;
                line-height: rem(36);
                @include ellipsis;
            }
        }
    }
}