@charset 'UTF-8';

.memberChange {
    position: relative;
    padding: rem(64) rem(30) rem(30);
    background: #fff;
    border:rem(14) solid #f7f7f7;
    border-top:0;
    .member__radio + .member__radio {
        margin-left: 20px;
    }
    .member__detailmajors {
        overflow: hidden;
    }
    .member__detailmajors .member__checkbox {
        float: left;
        width: 33.33%;
        margin: 9px 0;
    }
    .member__hospitalSize .member__radio {
        float: left;
        width: 24.9%;
        margin: 0;
    }
    .member__hospitalSize .member__input {clear:both} 
    .member__btnbox {
        .button {
            width: 100%;
        }
    }
}


