.memberSummary {
    padding: rem(28) rem(36) rem(36);
    .member__input {
        padding-left: rem(22);
    }
    &__list {
        margin-bottom: rem(12);
        font-size: rem(28);
        li {
            overflow: hidden;
            line-height: 1;
            padding: rem(30) rem(22) rem(32);
            border-bottom: rem(2) solid #f1f1f1;
            background: #fff;
        }
    }
    &__h3 {
        position: relative;
        margin-bottom: rem(26);
        padding-left: rem(26);
        font-size: rem(28);
        color: #242424;
        line-height: 1;
        &:not(:first-child){
            margin-top:rem(60);
        }
        &:first-child{
            margin-top:rem(24);
        }
        &:before {
            content:'';
            display: inline-block;
            position: absolute;
            top:calc(50% - 1px);
            left:0;
            width: 2px;
            height: 2px;
            border-radius: 50%;
            background: #242424;
        }

        + .memberSummary__list {
            margin-top: rem(42);
            border-top: 1px solid #e8e8e8;
            border-bottom: 1px solid #e8e8e8;
            padding: rem(8) 0;
            background: #f2f2f2;
        }
    }
    &__title {
        float: left;
        color: #5d5d5d;
    }
    &__desc {
        float: right;
        color: #333;
        &.red {
            color: #ff2929;
        }
        &.blue {
            color: $pointColor;
        }
    }
    &__noti {
        margin: rem(30) 0;
        font-size: rem(24);
        line-height: 1.5em;
        color: #949494;
    }
    &__finish {
        margin-top: 15vh;
        margin-bottom: rem(60);
        font-size: rem(30);
        line-height: 1.733em;
        color: #333;
        text-align: center;
        &:before {
            content:'';
            display: block;
            margin: 0 auto rem(80);
            width: rem(352);
            height: rem(166);
            background: url($url+'main/intermd_logo_balck.png') no-repeat 0 0;
            background-size: 100% auto;
        }
    }
    &__top {
        overflow: hidden;
        position: relative;
        box-sizing: border-box;
        padding-bottom: rem(132);
        min-height: calc(100vh - 8.8125rem);  //0.875rem 1.125rem 0.5rem 0.0625rem 3.4375rem 2.8125rem
    }
    &--bottom {
        position: absolute;
        bottom:0;
    }
}

.member {
    &__box {
        margin-bottom: rem(64);
    }
    &__title {
        font-size: rem(30);
        line-height: rem(32);
        margin-bottom: 6px;
        color: #333;
        @at-root {
            .important-title {
                &:after {
                    content: "*";
                    display: inline-block;
                    color: $pointColor;
                    width: 20px;
                    height: 20px;
                    font-size: 15px;
                    padding-left: 5px;
                }
            }   
        }
    }
    &__input {
        display: block;
        margin-bottom: rem(10);
        padding: 5px;
        width: 100%;
        height: 40px;
        line-height: 21px;
        box-sizing: border-box;
        border-bottom: solid 1px rgba(0,0,0,0.1) !important;
        box-shadow: none;
        font-size: 15px;
        color: #333;
        background-color: transparent;
        transition: all 250ms ease;
        -webkit-appearance: none;
        &::placeholder{
            font-size: 13px; 
            color: #adadad;
        }
        &:focus { 
            border-bottom-color:$pointColor !important; 
        }
        &--readonly {}
    }
    &__radio {
        display: inline-block;
        &Text {
            font-size: rem(30);
            &::before {
                content: "\f1db";
                display: inline-block;
                margin-right: rem(6);
                line-height: 1;
                vertical-align: text-top;
                font-family: FontAwesome;
                color: #a0a0a0;
                font-size: rem(40);
            }
        }
        input {
            @include notTextInput;
            &:checked {
                + .member__radioText {
                    &::before {
                        content: "\f05d";
                    }
                }
            }
        }
    }
    &__checkbox {
        display: inline-block;
        line-height: 1em;
        &Text {
            font-size: rem(30);
            &::before {
                content: "\f096";
                display: inline-block;
                margin-right: rem(6);
                width:21px;
                line-height: 1;
                vertical-align: middle;
                font-family: FontAwesome;
                color: #a0a0a0;
                font-size: rem(40);
            }
        }
        input {
            @include notTextInput;
            &:checked {
                + .member__checkboxText {
                    &::before {
                        content: "\f046";
                    }
                }
            }
        }
    }
    &__selectbox {
        margin-bottom: rem(10);
        width: 100%;
        height: 40px;
        line-height: 40px;
        border-radius: 0;
        border:none;
        border-bottom: 1px solid rgba(0,0,0,0.1);
        box-sizing: border-box;
        font-size: rem(26);
        color: #5d5d5d;
        box-shadow: none;
        -webkit-appearance: none;
        background: #FFFFFF url(data:image/png;base64,R0lGODlhDwAUAIABAAAAAP///yH5BAEAAAEALAAAAAAPABQAAAIXjI+py+0Po5wH2HsXzmw//lHiSJZmUAAAOw==) no-repeat 95% 50%;
        background-color: transparent;
    }
    &__guide {
        display: block;
        margin-top: rem(2);
        font-size: rem(24);
        line-height: rem(38);
        color: #949494;
        &.red {color: red;}
        &.blue {color: $pointColor;}
    }

    
    
    &__half {
        overflow: hidden;
        &--region {
            overflow: hidden;
            font-size: rem(24);
            .member__selectbox {
                width: 72%;
            }
            .member__halfLt {
                float: left;
                width: 50%;
            }
            .member__halfRt {
                width: 50%;
                float: right;
                // text-align: right;
            }
        }
    }
}

.myPhoneNumber {
