                border-bottom: rem(2) solid #e7e7e7;
                &::placeholder {
                    color: #b2b2b2 !important;
                }
            }
            .btn-20 {
                position: absolute;
                right: 0;
                top: rem(67);
                float: none;
                width: rem(141) !important;
                height: rem(64);
                font-size: rem(26);
                line-height: rem(64);
                color: #cecece;
                letter-spacing: rem(-0.39);
                border-radius: rem(10);
                background: #f7f7f7;
                border: 0;
                &.on {
                    color: #17549a;
                    background: rgba(63, 146, 240, 0.22);
                }
            }
            .authTimer {
                position: absolute;
                right: rem(22);
                top: rem(88);
                font-size: rem(26);
                line-height: rem(30);
                color: #e60000;
                letter-spacing: rem(-0.39);
            }
        }
    }
    &Wrap {
        position: relative;
        &__logout {
            position: absolute;
            right: 0;
            top: 0;
            padding: 0 rem(40);
            display: flex;
            height: rem(110);
            align-items: center;
            font-size: rem(32);
            line-height: rem(36);
            letter-spacing: rem(-0.48);
            color: #242424;
        }
    }
    &__con {
        padding: rem(140) rem(40) rem(30);
    }
    &__title {
        margin-bottom: rem(57);
        .tit {
            margin-bottom: rem(32);
            font-size: rem(42);
            font-weight: bold;
            line-height: rem(54.5);
            letter-spacing: rem(-0.63);
            color: #333;
        }
        .desc {
            font-size: rem(28);
            line-height: rem(40);
            letter-spacing: rem(-0.64);
            color: #666;
        }
    }
    &__row {
        position: relative;
        margin-bottom: rem(64);
        label {
            display: block;
            margin-bottom: rem(37);
            font-size: rem(28);
            line-height: rem(32);
            color: #666;
        }
        input {
            float: none;
            padding: rem(15) rem(167) rem(20) rem(26);
            width: 100%;
            height: rem(76);
            font-size: rem(34);
            line-height: rem(39);
            color: #000;
            letter-spacing: rem(-0.51);
            border-bottom: rem(2) solid #e7e7e7;
            box-sizing: border-box;
            &::placeholder {
                color: #b2b2b2;
            }
        }
        a {
            position: absolute;
            right: 0;
            top: rem(67);
            display: block;
            width: rem(141);
            height: rem(64);
            font-size: rem(26);
            line-height: rem(64);
            color: #cecece;
            letter-spacing: rem(-0.39);
            text-align: center;
            border-radius: rem(10);
            background: #f7f7f7;
            border: 0;
            &.on {
                color: #17549a;
                background: rgba(63, 146, 240, 0.22);
            }
        }
        .authTimer {
            position: absolute;
            right: rem(22);
            top: rem(88);
            font-size: rem(26);
            line-height: rem(30);
            color: #e60000;
            letter-spacing: rem(-0.39);
        }
    }
    &__toastMsg {
        display: flex;
        justify-content: center;
        position: fixed;
        z-index: 9999;
        width: calc(100% - #{rem(64)});
        bottom: rem(222);
        left: rem(32);
        visibility: hidden;
        &.show {
            visibility: visible;
            animation: fadein 0.5s, fadeout 0.5s 2.5s;
        }
        p {
            margin: 0 !important;
            font-size: rem(24) !important;
            line-height: rem(32) !important;
            letter-spacing: rem(-1);
            color: #fff;
            padding: rem(18) rem(20) !important;
            background-color: rgba(0,0,0,0.7);
            box-shadow: 0 rem(4) rem(8) 0 rgba(0, 0, 0, 0.5);
            border-radius: rem(8);
        }
        @keyframes fadein {
            from {bottom: 0; opacity: 0;}
            to {bottom: rem(222); opacity: 1;}
        }
        @keyframes fadeout {
            from {bottom: rem(222); opacity: 1;}
            to {bottom: 0; opacity: 0;}
        }
    }
}
