@charset 'UTF-8';

/* profile */
.profilefield {
    $sizeDef : 45px;
    $sizeSmall : 35px;
    $sizeXsmall : 30px;
    $size60 : 60px;

    display: block;
    width: 100%;
    height: $sizeDef;
    line-height:0;
    position: relative;
    .profile_list & { //  ,  
        display: flex;
        align-items: center;
        // >a,
        // .profilefield__link {
        //     display: block;
        //     flex:1;
        //     overflow: hidden;
        // }
        a,
        .profilefield__link {
            flex: 1 1 0;
            display: flex;
            align-items: center;
            .profilefield__img {
                display: block;
            }
            .profilefield__text{
                width: calc(100% - 51px);
                margin-left: 0;
                flex: 1 1 0;
                .profilefield__desc {
                    line-height: 1.2;
                }
            }
        }
        
        .rt {
            
            margin-left: auto;

            @at-root {
                .profilefield__link + .rt {
                    margin-left: rem(26);
                }
            }
        }
    }
    >a,
    .profilefield__link  {
        overflow: hidden;
        display: inline-block;
        font-size: 0;
    }
    &.type_small { // 
        height:$sizeSmall;
        .profilefield__text {
            margin: 0 0 1px 0;
            padding-left:5px;
            line-height: $sizeSmall;
        }
        .profilefield__name {
            margin: 0;
            font-size:13px;
            line-height:1em;
        }
        .profilefield__desc {
            margin-top:5px;
            line-height:1em;
            height: auto;
        }
        .profilefield__img {
            width:$sizeSmall;
            height:$sizeSmall;
        }
    }
    &.type_60 { //  
        height:$size60;
        .profilefield__text {
            margin: 0 0 1px 0;
            padding-left:9px;
            width: calc(100% - 70px);
            line-height: $size60;
        }
        .profilefield__name {
            margin: 0;
            font-size:16px;
            line-height:1em;
        }
        .profilefield__desc {
            margin-top:5px;
            line-height:1em;
            font-size: 14px;
            height: auto;
        }
        .profilefield__img {
            width:$size60;
            height:$size60;
        }
    }
    &.type_vertical {
        height: auto;
        text-align: center;
        .profilefield__text {
            display: block;
            margin-top:3px;
            padding-left:0;
            max-width: 100%;
        }
        .profilefield__name {
            margin: rem(24) 0 rem(16) 0;
            font-size:rem(38);
            color:#000;
            font-weight: bold;
        }
        .profilefield__desc {
            font-size:rem(28);
            color:#5d5d5d;
        }
        .profilefield__img {
            width:rem(204);
            height:rem(204);
        }
    }
    &.type_verticalMsg {
        height: auto;
        text-align: center;
        .profilefield__text {
            display: block;
            padding-left:0;
            max-width: 100%;
        }
        .profilefield__name {
            margin: rem(30) 0 rem(16) 0;
            font-size:rem(42);
            line-height: 1.2em;
            color:#242424;
            font-weight: normal;
        }
        .profilefield__desc {
            margin: 0 rem(103);
            font-size:rem(32);
            color:#8d8e9a;
            white-space: unset;
            text-overflow: unset;
        }
        .profilefield__img {
            width:rem(160);
            height:rem(160);
            box-shadow: 4px 4px 16px 0 rgba(108, 108, 120, 0.1);
            border: solid 1px #efeff2;
        }
    }
    &.type_myDashboard {        
        $dashbosrdSize : rem(120);
        height:$dashbosrdSize;
        font-size:0;
        .profilefield__text {
            margin: 0 0 rem(2) 0;
            padding-left:rem(10);
            line-height: $dashbosrdSize;
        }
        .profilefield__name {
            margin:rem(2) 0 rem(16) 0;
            font-size:rem(38);
            line-height:1em;
            color:#242424;
        }
        .follow {
            position: relative;
            padding-left: rem(32);
            font-size: rem(26);
            color: $pointColor;
            &:before {
                content:'';
                display: inline-block;
                width: rem(4);
                height: rem(4);
                border-radius: rem(4);
                position: absolute;
                top:calc(50% - 1px);
                left:rem(14);
                background: $pointColor;
            }
        }
        .following {
            position: relative;
            padding-left: rem(32);
            font-size: rem(26);
            color: #414141;
            &:before {
                content:'';
                display: inline-block;
                width: rem(4);
                height: rem(4);
                border-radius: rem(4);
                position: absolute;
                top:calc(50% - 1px);
                left:rem(14);
                background: #414141;
            }
        }
        .profilefield__desc {
            font-size: rem(28);
            line-height:1em;
            height: auto;
            color: #242424;
        }
        .profilefield__img {
            width:$dashbosrdSize;
            height:$dashbosrdSize;
        }
    }
    &.type_sideInfo {        
        $sideInfoSize : rem(120);
        height:$sideInfoSize;
        font-size:0;
        > a {
            overflow: visible; //   box-shadow  
        }
        .profilefield__text {
            margin: 0 0 rem(2) 0;
            padding-left:rem(22);
            line-height: $sideInfoSize;
        }
        .profilefield__name {
            margin:rem(2) 0 rem(20) 0;
            font-size:rem(38);
            line-height:1em;
            color:#fff;
        }
        .profilefield__desc {
            font-size: rem(26);
            line-height:1em;
            height: auto;
            color: #e9e9ed;
        }
        .profilefield__img {
            width:$sideInfoSize;
            height:$sideInfoSize;
            border: none;
            box-shadow:  0px 1px 14px 2px rgba(0, 0, 0, 0.4);
        }
    }

    &.type_mrProfile {        
        $mrProfileSize : rem(160);
        height:$mrProfileSize;
        font-size:0;
        width: calc(100% - #{rem(124)}); // 54 + 70
        .profilefield__text {
            margin: 0 0 rem(2) 0;
            padding-left:rem(26);
            line-height: $mrProfileSize;
            width: calc(100% - #{rem(186)});
        }
        .profilefield__name {
            margin:rem(2) 0 rem(20) 0;
            font-size:rem(42);
            line-height:1em;
            color:#242424;
            font-weight: normal;
        }
        .profilefield__desc {
            font-size: rem(32);
            line-height:1.3em;
            height: auto;
            color: #9f9f9f;
            @include ellipsisV(2);
            white-space: unset;
        }
        .profilefield__img {
            width:$mrProfileSize;
            height:$mrProfileSize;
        }
    }
    &__img {
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;
        border-radius: 50%;
        box-sizing: border-box;
        width: $sizeDef;height: $sizeDef;
        border: 1px solid #e5e5e5;
        @include c_img();
    }
    &__text {
        display: inline-block;
        vertical-align: middle;
        padding-left:6px;
        font-size: 13px;
        line-height: 31px;
        font-weight: bold;
        .solo {
            color: #333!important;
            font-size: 13px!important;
            line-height: 17px;
            font-weight: bold!important;
            position: relative;
            top: -1px!important;
        }
    }
    &__name {
        display: block;
        margin:0 0 4px 0;
        font-size:14px;
        line-height:13px;
        color:#333;
        font-weight: bold;
        white-space: nowrap;
    }
    &__desc {
        display: block;
        color:#9f9f9f;
        font-size: 12px;
        line-height: 1;
        font-weight: normal;
        white-space: nowrap;
        @include ellipsis();
    }
    .rt {
        // position: absolute;top:0;right:0;
        height:100%;
        vertical-align: middle;
        &:before {
            content: '';
            height: 100%;
            vertical-align: middle;
            display: inline-block;
        }
    }
}

.profilefield__badge {
    display: inline-block;
    width:rem(34);
    height:rem(34);
    line-height:rem(34);
    text-align: center;
    border-radius: 50%;
    font-size: rem(22);
    font-weight: normal;
    color: #fff;
    font-family: sans-serif;
    background: $pointColor;
    cursor: default;
}

.profileCard {
    position: fixed; 
    top: 0; bottom: 0; left: 0; right: 0; 
    width: 100%; height: 100vh; 
    z-index: 800;
    text-align: center;
    &:after {
        content:'';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
    }
    &__dim {
        position: fixed; 
        top: 0; bottom: 0; left: 0; right: 0; 
        width: 100%; height: 100vh; 
        z-index: 11; 
        background: rgba(0, 0, 0, 0.4); 
    }
    &__inner {
        position: relative;
        z-index: 12;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;
        margin-left: rem(8);
        padding: rem(42) rem(36);
        box-sizing: border-box;
        width:85%;
        max-width: rem(640);
        height:rem(650); 
        border-radius: 0.9375rem; 
        text-align: center;
        background: #fff;
    }
    &__btn { 
        position: absolute; 
        top: 0.40625rem; right: 0.40625rem; 
        padding: 0.6875rem; 
        color: #a0a0a0;
        background: transparent;
        i {
            width: 1.5rem; 
            height: 1.5rem; line-height: 1.5rem; 
            font-size: 1.5rem; 
            pointer-events: none;
        }
    }
    .profilefield {
        margin-bottom: rem(16);
    }

    .roundbtn {
        width:rem(270);
        height:rem(50);
        line-height:rem(50);
        border-radius: rem(26);
        border:1px solid #eeeeee;
        text-align: center;
        font-size: rem(24);
        color: #a0a0a0;
        background-color: #fff;
        &.on {
            color:#fff;
            border-color: $pointColor;
            background: $pointColor;
        }
    }
    &__btnBox {
        display: flex;
        align-items: center;
        justify-content: center;
        .roundbtn {
            max-width: 48%;
            &:last-child {
                margin-left: 4%;
            }
        }
    }
}

//    
.profile__wrapBtn {
    overflow: hidden;
    margin: rem(22) auto 0;
    width: 100%;
    max-width: rem(540);
}
.profileBtn{
    &__profile,
    &__msg,
    &__point {
        display: block;
        float: left;
        padding-top: rem(20);
        width:33%;
        text-align: center;
    }
    &__msg{
        width: 34%;
    }
    &__icon {
        display: inline-block;
        margin-bottom: rem(18);
        width:rem(96);
        height:rem(96);
        border-radius: rem(96);
        background: #f3f3f3;
        &:after {
            content:'';
            display: inline-block;
            vertical-align: middle;
            text-align: center;
            .profileBtn__profile & {
                @include _003_icon_profile;
            }
            .profileBtn__msg & {
                @include _003_icon_message;
            }
            .profileBtn__point & {
                @include _003_icon_point;
            }
        }
        &:before {
            content: '';
            display: inline-block;
            vertical-align: middle;
            height: 100%;
        }
    }
    &__txt {
        display: block;
        font-size: rem(24);
        line-height: 1;
        color: #333;
    }
}

.btn_myVdtMr {
    display: inline-block !important;
    vertical-align: middle;
    line-height: 1;
    &::after {
        content:'';
        display: inline-block;
        @include vDt_myBooking_review(36,36);
    }
}
.profile__starBtn {
    display: inline-block;
    margin-right: rem(24);
    margin-left: rem(-10);
    padding: rem(10);
    width: rem(36);
    height: rem(36);
    line-height: rem(60);
    box-sizing: content-box;

    &.on:after {background-color: $pointColor;}
    &:after {
        content:'';
        display: inline-block;
        @include my-vdetailing-mrhistory-star(36,36);
        vertical-align: top;
        background-color: #efeff2;
    }
}
.profilefield__name.-star {
    font-weight: bold;
    color: $pointColor;
    &:before {
        content:'';
        display: inline-block;
        margin-right: rem(12);
        @include my-vdetailing-mrhistory-star(26,26);
        background-color: $pointColor;
    }
}
.profile__guideStar {
    padding: rem(26) $commSidePadding 0;
    font-size: rem(22);
    color: $pointColor;
    border-top: rem(16) solid $subLineColor;

    &:before {
        content:'';
        display: inline-block;
        margin-top: rem(-4);
        margin-right: rem(12);
        vertical-align: middle;
        @include my-vdetailing-mrhistory-star(26,26);
        background-color: $pointColor;
    }
}

// MR history  
.mrProfile {
    position: relative;
    padding: rem(62) rem(32) rem(126) rem(34);
    background: #f4f4f6;

    + .mrProfile__cont {
        position: relative;
        top: rem(-66);
        z-index: 1;
    }
    &__cont {
        padding: rem(60) $commSidePadding rem(110);
        border-radius: rem(34);
        background: #fff;
    }
}