@charset 'UTF-8';

// .header {..} 3.0 native  
.header {
    // position:sticky;
    position: fixed;
    top: 0;
    z-index: 800;
    // overflow: hidden; md    
    width: 100%;
    .container {
        position: relative;
        margin-bottom: 0;
        height: rem(110);
        background-color: #242424;
        padding: rem(22) rem(22) 0 0;
        text-align: right;
        box-sizing: border-box;
    }
    &__logo {
        position: absolute;
        top:0;
        left:0;
        display: inline-block;
        padding: rem(28) 0 0 rem(32);
        a {
            display: block;
            width: rem(140);
            height: rem(54);
            background: url($url+'common/logo.svg') no-repeat 50%;
            background-size: contain;
        }
    }
    &__alarm ,
    &__searchBtn ,
    &__mdschIcon {
        margin-left: rem(10);
    }
    &__alarm {
        position: relative;
        display: inline-block;
        padding: rem(12);
        text-align: center;
        line-height: 1;
        &:after {
            content: '';
            display: block;
            @include _003_icon_alarm;
        }
        .num {
            position: absolute;
            right: 0;
            top: 0;
            width: auto;
            min-width: rem(32);
            height: rem(32);
            border-radius: rem(32);
            background-color: #f72053;
            color: #fff;
            text-align: center;
            font-size: rem(20);
            line-height: rem(32);
        }
    }
    &__searchBtn {
        right:rem(92,1);
        display: inline-block;
        padding: rem(12);
        line-height: 1;
        &:after {
            content: '';
            @include _003_icon_search;
        }
    }

    // MD 
    &__mdschIcon {
        padding: rem(12);
        display: inline-block;
        line-height: 1;
        &:after {
            content:'';
            display: inline-block;
            @include header-schedule(38,38)
        }
    }
    &__mdsch {
        &Lyr {
            position: absolute;
            top: rem(90);
            right:rem(46);
            &Inr {
                padding: 0 rem(70) 0 rem(22);
                height: rem(62);
                line-height: rem(64);
                border-radius: rem(8);
                box-shadow: 3.2px 2.4px 6px 0 rgba(0, 0, 0, 0.15);
                border: solid rem(2) #606060;
                background: #fff;
                font-size: rem(24);
                color: #242424;
                b {
                    color: #73b5ff;
                }
            }
            &:before {
                content:'';
                position: absolute;
                top: rem(-6);
                right: rem(76);
                z-index: 1;
                width: 0px;height: 0px;
                border-top:none;
                border-bottom:rem(8) solid #fff;
                border-right: rem(8) solid transparent;
                border-left: rem(8) solid  transparent;
            }
            &Close {
                display: inline-block;
                position: absolute;
                top:rem(0);
                right:rem(4);
                padding: rem(16);
                width: rem(30);
                height: rem(30);
                box-sizing: content-box;
                &:before {
                    content:'';
                    display: inline-block;
                    @include scheduler-calendar-setting-close(30,30)
                }
            }
        }
    }
}


//  
.gnbAlarm {
    &__wrap {
        overflow-y: auto;
        -webkit-overflow-scrolling: touch;
    }
    &__top {
        overflow: hidden;
        height: rem(66);
        line-height: 1;        
        margin:0 $commSidePadding;
        border-bottom: rem(2) solid #f2f2f2;
        .lt {
            float: left;
            font-size: rem(26);
            line-height: rem(66);
            color: #242424;
        }
        .rt {
            float: right;
            font-size: 0;

            button {
                &:last-child {
                    &:before {
                        content:'';
                        display: inline-block;
                        margin: 0 rem(16) 0;
                        vertical-align: middle;
                        width:1px;
                        height:rem(30);
                        background: #eaeaea;
                    }
                }
            }
        }
        .num {
            color: #f22a56;
            font-weight: bold;
            font-family: serif;
        }
        .readAll,
        .deleteAll {
            font-size: rem(26);
            line-height: rem(62);
            color: #7d7d7d;
        }
    }
    &__list {
        // height: rem(829);
        li {
            position: relative;
            &:after {
                content:'';
                display: block;
                margin:0 $commSidePadding;
                border-bottom: rem(2) solid #f2f2f2;
            }
        }
        .new {
            &:before {
                content: "";
                display: block;
                width: rem(10);
                height: rem(10);
                border-radius: 50%;
                background-color: #f72053;
                position: absolute;
                top: rem(46);
                left: rem(16);
            }
        }
        a {
            display: block;
            box-sizing: border-box;
            padding: rem(30) $commSidePadding rem(22);
            width:90.6%;
        }
    }
    &__tit {
        display: block;
        margin-bottom: rem(4);
        font-size: rem(30);
        line-height: 1.5em;
        color: #242424;
    }
    &__cont {
        display: block;
        font-size: rem(26);
        line-height: 1.429em;
        color: #7d7d7d;
        @include ellipsisV(2);
    }
    &__time {
        font-size: rem(22);
        color: #b0b0b0;
    }
    &__del {
        position: absolute;
        top:rem(20);
        right:rem(22);
        padding:5px;
        font-size: rem(36);
        line-height: 1;
        color: #7d7d7d;
    }
    &__text {
        .gnbAlarm__list + & {
            margin: rem(84) 0;
        }
        margin-top: rem(178);
        text-align: center;
        font-size: rem(26);
        color: #7d7d7d;
        b {
            font-size: rem(30);
            color: #242424;
            font-weight: normal;
        }
    }
    &__holdReason {
        padding: rem(20) rem(24);
        margin: rem(13) 0;
        width: 100%;
        box-sizing: border-box;
        background: #fafafa;
        &Tit {
            font-size: rem(22);
            font-weight: bold;
            color: #888;
            line-height: rem(32);
        }
        ul {
            li {
                margin-top: rem(8);
                font-size: rem(24);
                color: #333;
                line-height: rem(35);
            }
        }
    }
}
