#baguetteBox-overlay{display:none;opacity:0;position:fixed;overflow:hidden;top:0;left:0;width:100%;height:100%;z-index:1000000;background-color:#222;background-color:rgba(0, 0, 0, 1);-webkit-transition:opacity .35s ease;transition:opacity .35s ease}#baguetteBox-overlay.visible{opacity:1}#baguetteBox-overlay .full-image{display:inline-block;position:relative;width:100%;height:calc(100%  - 40px);text-align:center}#baguetteBox-overlay .full-image figure{display:inline;margin:0;height:100%}#baguetteBox-overlay .full-image img{display:inline-block;width:auto;height:auto;max-height:100%;max-width:100%;vertical-align:middle;-webkit-box-shadow:0 0 8px rgba(0, 0, 0, .6);-moz-box-shadow:0 0 8px rgba(0, 0, 0, .6);box-shadow:0 0 8px rgba(0, 0, 0, .6)}#baguetteBox-overlay .full-image figcaption{display:block;position:absolute;top:0;width:100%;text-align:center;line-height:1.8;white-space:normal;color:#FFFFFF;background-color:#000;background-color:rgba(0, 0, 0, 1) !important;font-family:sans-serif;height:50px;margin-top:20px;font-size:12px}#baguetteBox-overlay .full-image:before{content:"";display:inline-block;height:50%;width:1px;margin-right:-1px}
#baguetteBox-slider{position:absolute;left:0;top:0;height:100%;width:100%;white-space:nowrap;-webkit-transition:left .4s ease, -webkit-transform .4s ease;transition:left .4s ease, -webkit-transform .4s ease;transition:left .4s ease, transform .4s ease;transition:left .4s ease, transform .4s ease, -webkit-transform .4s ease, -moz-transform .4s ease}#baguetteBox-slider.bounce-from-right{-webkit-animation:bounceFromRight .4s ease-out;animation:bounceFromRight .4s ease-out}#baguetteBox-slider.bounce-from-left{-webkit-animation:bounceFromLeft .4s ease-out;animation:bounceFromLeft .4s ease-out}@-webkit-keyframes bounceFromRight{0%,100%{margin-left:0;}50%{margin-left:-30px;}}@keyframes bounceFromRight{0%,100%{margin-left:0;}50%{margin-left:-30px;}}@-webkit-keyframes bounceFromLeft{0%,100%{margin-left:0;}50%{margin-left:30px;}}@keyframes bounceFromLeft{0%,100%{margin-left:0;}50%{margin-left:30px;}}.baguetteBox-button#next-button,.baguetteBox-button#previous-button,.baguetteBox-button#close-button{width:50px;height:40px;bottom:0px;line-height:40px;}.baguetteBox-button{position:absolute;cursor:pointer;outline:0;padding:0;margin:0;border:0;background-color:#323232;background-color:rgba(50, 50, 50, .5);color:#ddd;font:1.6em sans-serif;-webkit-transition:background-color .4s ease;transition:background-color .4s ease}
.baguetteBox-button#next-button{right:0px;width:33%;font-size:28px;text-align:right;padding-right:20px}
.baguetteBox-button#previous-button{width:33%;left:0px;font-size:28px;text-align:left;padding-left:20px}
.baguetteBox-button#close-button{font-size:25px;width:50px;left:33%;width:34%}.baguetteBox-button svg{position:absolute;left:0;top:0}.baguetteBox-spinner{width:40px;height:40px;display:inline-block;position:absolute;top:50%;left:50%;margin-top:-20px;margin-left:-20px}.baguetteBox-double-bounce1,.baguetteBox-double-bounce2{width:100%;height:100%;-moz-border-radius:50%;border-radius:50%;background-color:#fff;opacity:.6;position:absolute;top:0;left:0;-webkit-animation:bounce 2s infinite ease-in-out;animation:bounce 2s infinite ease-in-out}.baguetteBox-double-bounce2{-webkit-animation-delay:-1s;animation-delay:-1s}@-webkit-keyframes bounce{0%,100%{-webkit-transform:scale(0);transform:scale(0);}50%{-webkit-transform:scale(1);transform:scale(1);}}@keyframes bounce{0%,100%{-webkit-transform:scale(0);-moz-transform:scale(0);transform:scale(0);}50%{-webkit-transform:scale(1);-moz-transform:scale(1);transform:scale(1);}}.baguetteBox-button{display:block !important}
