@import 'variable';

	@mixin _001_nav_EMR($ratio:2,$onlyPos:all) {
		@if $onlyPos == pos {
			background-position: (-430px / $ratio) (-476px / $ratio);
			@if $ratio != 2 {
				background-size:(664px / $ratio) (606px / $ratio);
			}
		} @else {
			display:inline-block;
			width: (54px / $ratio);
			height: (54px / $ratio);
			background-position: (-430px / $ratio) (-476px / $ratio);
			background-size:(664px / $ratio) (606px / $ratio);
			background-image: url($spriteURL);
			background-repeat:no-repeat;	
		}
	}
