@charset 'UTF-8';

.nav {
    position: fixed;
    // position: sticky;
    // position: -webkit-sticky;
    bottom: 0;
    z-index: 2;
    width: 100%;
    h1 {
        margin: 0;
    }
    &__menu {
        overflow: hidden;
        border-top: 1px solid #eaeaea;
        height: rem(98);
        background-color: #ffffff;
        a {
            float: left;
            display: block;
            width: 20%;
            padding: rem(18) 0 0 0;
            height: rem(98);
            box-sizing: border-box;
            text-align: center;
            line-height: 0;
            &:before {
                content: '';
                margin: 0 auto;
            }
            &.on {
                span {
                    color: $pointColorB;
                }
            }
            span {
                display: block;
                margin-top: rem(10);
                font-size: rem(18);
                line-height: 1;
                color: #a09f9f;
                pointer-events: none;
                -webkit-user-select: none;
            }
        }
    }
    &__home {
        &:before {
            @include _001_nav_home(4);
        }
        &.on:before {
            @include _001_nav_home_on(4,pos);
        }
    }
    &__qna {
        &:before {
            @include _001_nav_qna(4);
        }
        &.on:before {
            @include _001_nav_qna_on(4,pos);
        }
    }
    &__posting {
        &:before {
            @include _001_nav_posting(4);
        }
        &.on:before {
            @include _001_nav_posting_on(4,pos);
        }
    }
    &__poll { //  poll-webinar   (20200911)
        &:before {    
            display: inline-block;
            width: 20px;
            height: 20px;
            @include nav_poll(40,40);
        }
        &.on:before {
            @include nav_poll_on(40,40);
        }
    }
    &__more {
        &:before {
            @include _001_nav_more(4);
        }
        &.on:before {
            @include _001_nav_more_on(4,pos);
        }
    }
    &__seminar {
        &:before {
            @include _001_nav_seminar(4);
        }
        &.on:before {
            @include _001_nav_seminar_on(4,pos);
        }
    }
}
.navMoreMenu {
    display: none;
    &.on {
        display: block;
    }
    &:after {
        content:'';
        display: block;
        position: fixed;
        bottom:rem(108);
        right:9%;
        z-index: 801;
        width: 0px;
        height: 0px;
        border-left: rem(10) solid transparent;
        border-right: rem(10) solid transparent;
        border-top: rem(10) solid #fff;
    }
    &__swiper {
        position: fixed;
        bottom:rem(118);
        left: 4%;
        z-index: 801;
        display: block;
        width: 92%;
        padding: rem(20) rem(20) rem(50);
        height: rem(356);
        box-sizing: border-box;
        border-radius: rem(16);
        background: #fff;
        a {
            float: left;
            display: block;
            width: 33.33%;
            padding: rem(40) 0 0 0;
            height: rem(142);
            box-sizing: border-box;
            text-align: center;
            line-height: 0;
            &:before {
                content: '';
                margin: 0 auto;
            }
            &.on {
                span {
                    color: $pointColorB;
                }
            }
            span {                
                display: block;
                margin-top: rem(10);
                font-size: rem(24);
                line-height: 1;
                letter-spacing: -.027em;
                color: #242424;
                pointer-events: none;
                -webkit-user-select: none;
            }
        }
    }
    &__dim {
        position: fixed;
        top: 0;
        bottom: rem(100);
        left: 0;
        right: 0;
        width: 100%;
        z-index: 800;
        opacity: 0;
        background: rgba(0, 0, 0, 0.7);
        -webkit-transition: opacity .2s;
        transition: opacity .2s;

        .on & {
            opacity: 1;
        }
    }
    &__pagination {
        position: absolute;
        bottom: 0 !important;
        margin-bottom: rem(20);
        line-height: 0;
        text-align: center;
        .swiper-pagination-bullet{
            margin: 0 rem(6) !important;
            width:rem(8);
            height:rem(8);
            border-radius: rem(4);
            opacity: 1;
            background: #d7d7d7;
            transition:all .5s;
        } 
        .swiper-pagination-bullet-active {
            background: #242424;
        }
    }
    &__poll {
        &:before {
            @include _001_nav_poll(4);
        }
    }
    &__seminar { //  poll-webinar   (20200911)
        &:before {
            display: inline-block;
            width: 27px;
            height: 27px;
            @include navMoreMenu_webinar(54,54)
        }
    }
    &__EMR {
        &:before {
            @include _001_nav_EMR(4);
        }
    }
    &__schedule {
        &:before {
            @include _001_nav_schedule(4);
        }
    }
    &__utility {
        &:before {
            @include _001_nav_utility(4);
        }
    }
    &__event {        
        &:before {
            @include _001_nav_event(4);
        }
    }
    &__mypoint {
        &:before {
            @include _001_nav_mypoint(4);
        }
    }
    &__my {
        &:before {
            @include _001_nav_my(4);
        }
    }
    &__notice {
        &:before {
            @include _001_nav_notice(4);
        }
    }
    &__cs {
        &:before {
            @include _001_nav_cs(4);
        }
    }
    &__faq {
        &:before {
            @include _001_nav_faq(4);
        }
    }
    &__career {
        &:before {
            @include _001_nav_career(4);
        }
    }
}
