@charset 'UTF-8';

// S: 2.0 , media....
.snb {
    position: sticky;
    position: -webkit-sticky;
    top:$headerH;
    z-index: 797;    
    // margin-bottom: rem(10);
    height:rem(96);
    border-bottom: 1px solid #e9e9e9;
    box-sizing: border-box;
    .page_poll & {
        margin-bottom: 0;
    }
    + .tabSort {
        margin-top: - rem(10);
    }
    &:after {
        content:'';
        display: block;
        clear:both;
    }
    &Swiper {
        padding:0 3px;
        box-sizing:border-box;
        width:100%;
        background: #fff;
        // .page_posting &,  // posting      
        .page_qna &{
            float:left;
            width:calc(100% - 43px);
        }
        .page_media &{
            padding:0 calc(#{rem(32)} - 5px);
        }
        &:not(.swiper-container){
            padding:0;
            width:100%;
            .swiper-wrapper {
                justify-content: space-around;
                -webkit-justify-content: space-around;
            }
            .page_poll & {
            }
            .swiper-slide {
                width:33.33%;
            }
        }
        .swiper-wrapper {        
            box-sizing: content-box;
            -webkit-box-pack: justify;
            -webkit-justify-content: space-between;
            justify-content: space-between;
        }
        .swiper-slide {         
            -webkit-transform-style: preserve-3d;
            -moz-transform-style: preserve-3d;
            -ms-transform-style: preserve-3d;
            transform-style: preserve-3d;
            width: auto;
            a {
                display: block;
                text-align: center;
            }
            .text {
                margin:0 5px;
                padding:16px 5px 8px 5px;
                box-sizing:border-box;
                position: relative;
                display: inline-block;
                height:rem(94);
                white-space:nowrap;
                font-size: rem(32);
                line-height: 1em;
                color:#333;
            }
        }
        .on {
            .text {
                color:#242424;
                font-weight: bold;
            }
            .text:after {
                content:'';
                display: block;
                position: absolute;
                bottom:0;left:0;
                width: 100%;
                height:rem(2);
                background: #242424;
            }
        }
    }
    &__moreBtn {
        display: block;
        float:right;
        height: 47px;
        line-height: 52px;
        width:43px;
        border-left:1px solid #e9e9e9;
        font-size: 16px;
        color:$pointColor;
        background: #fff;
    }
}
.menuAll {
    display: none;
    position: absolute;
    top:0; left:0;
    z-index:800;
    width:100%;
    &__top {
        overflow: hidden;
        height:47px;
        border-bottom:1px solid #e9e9e9;
        background: #fff;
    }
    &__title {
        float: left;
        padding-left: rem(30);
        box-sizing:border-box;
        width:calc(100% - 43px);
        height:47px;
        line-height: 48px;
        font-size: rem(32);
        background: #fff;
    }
    &__boxmenu {
        padding:rem(30) rem(32);
        background: #fafafa;
    }
    &__close {
        float: right;
        display: block;
        width:43px;
        height:47px;
        line-height: 52px;
        font-size: 16px;
        color:$pointColor;
        border-left:1px solid #e9e9e9;
        background: #fff;
    }
    &__dim {
        // position: fixed;
        // top: 0;
        // bottom: 0;
        // left: 0;
        // right: 0;
        // z-index: 11;
        width: 100%;
        height: 100vh;
        background: rgba(0, 0, 0, 0.7);
        transition: opacity .2s;
    }
}
//  ..
.tabSort {
    position: sticky;
    position: -webkit-sticky;
    top: rem(206); //96+110 
    z-index: 790;
    display: flex;
    background: #fff;
    &.hide {
        display: none;
    }
    &__item {
        width: 100%;
        text-align: center;
        border-bottom: rem(2) solid #dfdfdf;
        a {
            display: block;
            overflow: hidden;
        }
        .text {
            display:block;
            width:100%;
            margin:rem(22) 0 rem(20) 0;
            height:rem(32);
            line-height: rem(32);
            // padding: 1px 0 0 0;
            font-size: rem(28);
            color:#9f9f9f;
        }
        &:not(:first-child) a:after{
            content: '';
            height: 16px;
            width: 1px;
            background: #e0e0e2;
            display: block;
            position: absolute;
            top: 50%;
            margin-top: -8px;
        }
        &.on {
            .text {
                position: relative;
                color:#242424;
                font-family: 'AppleSDGothicNeo-SemiBold';
                font-weight: bold;
            }
        }
    }
}



// filter
.filter {
    &Bar {
        position: relative;
        padding: rem(18) rem(32);
        min-height: rem(104);
        line-height: rem(104-(18*2));
        box-sizing: border-box;
        background:  #f8f8fa;
        &__lt {
            float: left;
        }
        &__rt {
            float: right;
        }
        &:after {
            content:'';
            display: block;
            clear: both;
        }
    }

    &Btn {
        position: relative;
        z-index: 2;
        display: inline-block;
        padding: 0 rem(26);
        height: rem(66);
        line-height: rem(66);
        box-sizing: border-box;
        font-size: rem(26);
        color: #878792;
        vertical-align: top;
        
        &Box {
            position: relative; // bg js (  .position().left)
            display: inline-block;
            vertical-align: top;
            border-radius: rem(32);
            height: rem(66);
            font-size: 0;
            background: #e4e4e8;
            box-sizing: border-box;
            * {
                box-sizing: border-box;
            }
        }
        &--bg {
            display: none;
            /*
            position: absolute;
            top:0; left:0;
            width: 0;
            z-index: 1;
            height: rem(66);
            border-radius: rem(32);
            color: #282828;
            box-shadow: rem(1) rem(1) rem(12) rem(1) rgba(0, 0, 0, 0.1);
            background: #fff;
            transition:left .3s, width .3s;
            */
        }

        &.on {
            font-weight: bold;
            color: #282828;
            box-shadow: rem(1) rem(1) rem(12) rem(1) rgba(0, 0, 0, 0.1);
            border-radius: rem(32);
            background: #fff;
        }
        @at-root {
            @media screen and (max-width:340px) {
                .filterBtn {
                    padding: 0 rem(20);
                }
            }
        }

    }
    &BtnChbox {
        display: inline-block;
        font-size: rem(26);
        line-height: 1.2;
        color: #a4a4ac;
        vertical-align: middle;
        &::before {
            content:'';
            display: inline-block;
            margin-right: rem(16);
            vertical-align: top;
            border-radius: rem(4);
            opacity: .4;
            @include web-seminar-registration-top(30,28);
        }

        &.on {
            color: #242424;
            &::before {
                opacity: 1;
            }
        }
    }
    &List {
        position: absolute;
        top:100%;
        right:0;
        display: none;
        width:100%;
        

        &__select {
            &.on {
                & + .filterList {
                    display: block;
                }
                &:after {
                    transform: rotate(180deg);
                }
            }
            font-size: rem(26);
            color: #242424;
            vertical-align: middle;
            &:after {
                content:'';
                display: inline-block;
                margin-left: rem(12);
                vertical-align: baseline;
                @include filterSelArrow(20,20);
            }
        }
        &__wrap {
            position: relative;
            z-index: 12;
            padding: rem(36) 0;
            background: #fff;
        }
        &__dim {
            display: block;
            position: absolute;
            top:100%; left: 0; right: 0;bottom: 0;
            z-index: 11;
            height: calc(100vh - 100%);
            width: 100vw;
            background: rgba(0,0,0,.6);
        }
        &__item {
            display: block;
            padding: 0 rem(42);
            width: 100%;
            height: rem(98);
            line-height: rem(98);
            text-align: left;
            background: #fff;
        }
        &__text {
            display: block;
            position: relative;
            display: block;
            font-size: rem(30);
            color: #828284;
            &:after {
                content:'';
                position: absolute;
                top:calc(50% - #{rem(34/2)});
                right:0;
                display: inline-block;
                @include filterLayerItem(34,34);
                opacity: .2;
            }
            .on & {
                font-weight: bold;
                color: #242424;

                &:after {
                    opacity: 1;
                }
            }
        }
    }
}

// -
.subCate {
    display: flex;
    flex-wrap: wrap;
    border-bottom: rem(10) solid $subLineColor;
    &__item {
        flex:1;
        position: relative;
        height: rem(94);
        line-height: rem(94);
        font-size: rem(30);
        color: #828282;
        text-align: center;

        &.on {
            color: #242424;
            font-weight: bold;
        }

        &:not(:first-child):before {
            content:'';
            display: block;
            position: absolute;
            top: calc(50% - #{rem(2)});
            left:0;
            border-radius: 50%;
            width: rem(6);
            height: rem(6);
            background: #d5d5d8;
        }
    }
}


// S:   
.boxMenu {
    position: relative;
    overflow: hidden;
    border-right: solid #e9e9e9 1px;
    border-top: solid #e9e9e9 1px;
    background: #fafafa;    
    
    &:before {
        content: '';
        display: block;
        width:100%;
        height: 1px;
        position: absolute;
        bottom:0;
        left: 0;    
        border-bottom: solid #e9e9e9 1px;
    }
    &:after {
        content: '';
        display: block;
        width: calc(33.33% + 1px);
        height: 40px;
        border-right: 1px solid #e9e9e9;
        border-left: 1px solid #e9e9e9;
        position: absolute;
        bottom: 1px;
        left: calc(33.33% - 0px);
        box-sizing: border-box;
        z-index: 1;
        pointer-events: none;
    }
    a {
        float: left;
        width:33.33%;
        height: 41px;
        line-height: 41px;
        border-left: 1px solid #e1e1e1;
        border-bottom: 1px solid #e1e1e1;
        font-size: rem(28);
        color:#9f9f9f;    
        box-sizing: border-box;
        text-align: center;
        background: #fff;
    }
    .on {
        color: #242424;
        font-weight: bold;        
    }
    .writeCate & {
        color: #a0a0a0;
        .on {
            color: #fff;
            background: #5d5d5d;
        }
    }
}
.writeCate {
    // margin: 27px 18px 30px 18px;
    margin: 0 0 30px 0;
    &__title {
        font-size: rem(30);
        color:#333;
        line-height:1em;
    }
    .boxMenu {
        margin-top: rem(30);
    }
}
