// qna 
.topic {
    &__header {
        overflow: hidden;
        padding: rem(36) rem(36) 0 rem(36);
        width: calc(100% - #{rem(36*2)});
        height: rem(94 - 36);
        box-sizing: content-box;
        border-bottom: rem(10) solid $subLineColor;
        background: #fff;
    }
	&__text {
        float:left;
		font-size:rem(24); 
		line-height: 1.1;
        color:#7d7d7d;
        white-space:nowrap;
	}
	&__no {
		font-weight: bold; 
		color:$pointColor;
	}
	&__btbox {
        // position: absolute;
        // top:rem(26); right:rem(30);
        float:right;
		a {
            display: block;
			margin: -7px -5px 0 0;
			padding: 5px;
			font-size: rem(26);
            line-height: 1;
			color:#242424;
            font-weight: bold;
            &::before {
                content:'';
                display: inline-block;
                margin-right: rem(14);
                vertical-align: bottom;
                @include career-filter(28,28);
            }
		}
	}
}